import { AfterViewInit, ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild, ViewEncapsulation } from '@angular/core';
import { VatService } from '@modules/vat/shared';
import { BsModalService } from 'ngx-bootstrap';
import ArrayStore from 'devextreme/data/array_store';
import DataSource from 'devextreme/data/data_source';
import { Helper } from '@core/helper/Helper';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthService, SocketService } from '@core/services';
import { TranslateService } from '@ngx-translate/core';
import { CustomersService } from '@modules/customers/shared';
import { VChanggeMoneyService } from '@core/library/invoice/v-changge-money.service';
import { Invoice } from '@modules/invoice-buy/shared/class/invoice.model';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { Library } from '@core/library';
import { FormHeaderInvoiceComponent } from '@modules/invoice-buy/component/form-header-invoice/form-header-invoice.component';
import { ProductGripComponent } from '@modules/invoice-buy/component/product-grip/product-grip.component';
import { Subscription } from 'rxjs/Subscription';
import { VStringService, sEnum } from '@core/index';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';

@Component({
  selector: 'app-invoice',
  templateUrl: './invoice.component.html',
  styleUrls: ['./invoice.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class InvoiceComponent implements OnInit, OnDestroy, AfterViewInit {
  @ViewChild(ProductGripComponent) productGrip: ProductGripComponent;
  @ViewChild(FormHeaderInvoiceComponent) formInvoice: FormHeaderInvoiceComponent;
  frmData: Invoice = new Invoice('1');
  frmTraLai = {
    reason: ''
  };
  is_multi_vat: any;
  isHdGTGT = true;
  invoice_products: ArrayStore;
  dataSource: DataSource;
  formatter: any = {};
  extentionGrid: any = {};
  idInvoice: string = null;
  _objTemplateSelect: any = {};
  _translate: any;
  system_parameter: any = {};
  fileData: Array<any> = [];
  ttDaXuatHoaDon = false; // Trng thi  xut ha n hay cha
  isAccessExportInv = false;
  isView = true;
  _isValid = false;
  configs: any;
  invoiceExp = "MO_RONG_FILE";
  ioSignature: Subscription;
  noiDungNguoiBan: any;
  noiDungFileHoaDon: any;
  typeInvoices = [];
  isReadOnly = false;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private modalService: BsModalService,
    private vatService: VatService,
    private socketService: SocketService,
    private authService: AuthService,
    private cdRef: ChangeDetectorRef,
    public translate: TranslateService,
    private customersService: CustomersService,
    private invoiceBuyService: InvoiceBuyService
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngAfterViewInit() {
  }

  getFileData(fileData) {
    this.fileData = fileData;
  }

  ngOnInit() {
    let dataValue = this.frmData;
    if (this.route.snapshot.routeConfig.path === 'view-invoice/:id') {
      this.isView = false;
    }
    this.configs = this.authService.getConfigs();
    this.typeInvoices = VSelectBoxService.loaiHD();
    this.connectWebsocket();
    this.route.data
      .map(value => value.FormResolverService)
      .subscribe((data: any) => {
        this.setConfig(data[1]);
        if (data.length > 3) {
          this.idInvoice = this.route.snapshot.params['id'];
          dataValue = data[3].input_invoice;
          dataValue.input_invoice_products = data[3].input_invoice.input_invoice_products;
          dataValue.input_invoice_files = data[3].input_invoice.input_invoice_files;
          this.fileData = data[3].input_invoice.input_invoice_files;
          this.frmData = dataValue;
          if (VStringService._attemptConvertBool(this.frmData.export_from_ihd)) {
            this.isReadOnly = true;
            this.frmData.lookup_link = "https://ihoadon.vn/kiem-tra/";
          }
          if (dataValue.decimal_places_json)
            this.formatter = VChanggeMoneyService.getFormatterColumnInv(JSON.parse(dataValue.decimal_places_json));
          this.createValueDataSource(dataValue.input_invoice_products);
          if (VStringService._getValueToString(this.frmData.version_xml) == "1") {
            if (VStringService._attemptConvertBool(this.frmData.is_import_from_zip)) {
              this.thongtinBenBan(false);
              this.verifySignature();
            }
            else {
              this.viewThongTinBenBan(this.frmData.json_data);
              this.fillDataToForm(this.frmData.json_data);
            }
          }
          else {
            this.thongtinBenBan(false);
            this.verifySignature();
          }
        } else {
          this.khoiTaoKhiThemMoi();
        }
      });
  }

  createValueDataSource(value) {
    this.invoice_products = new ArrayStore({
      key: 'id',
      data: this.createDataSource(value)
    });
    this.dataSource = new DataSource({
      store: this.invoice_products
    });
  }

  createDataSource(value) {
    const arr = [];
    value.forEach(item => {
      arr.push({
        id: Helper.makeid(),
        order: parseInt(item.order, 10),
        product_code: item.product_code,
        product_name: item.product_name,
        unit_code: item.unit_code,
        unit_name: item.unit_name,
        quantity: item.quantity,
        price: item.price,
        vat: item.vat,
        amount_vat: item.amount_vat,
        amount: item.amount,
        product_group: item.product_group,
        amount_after_vat: item.amount_after_vat
      });
    });
    let order1 = arr.length;
    for (let i = 0; i < 2; i++) {
      arr.push({
        id: Helper.makeid(),
        order: order1 + 1,
        product_code: '',
        product_name: '',
        unit_code: null,
        unit_name: '',
        quantity: null,
        price: null,
        vat: null,
        amount_vat: null,
        amount: null,
        product_group: null,
        amount_after_vat: null
      });
      order1++;
    }
    return arr;
  }

  khoiTaoKhiThemMoi() {
    // khi to mi li
    this.invoice_products = new ArrayStore({
      key: 'id',
      data: this.add_invoice_products_data()
    });
    this.dataSource = new DataSource({
      store: this.invoice_products
    });
    this.frmData.version_xml = "1";
  }

  add_invoice_products_data() {
    const arr = [];
    for (let i = 0; i < 5; i++) {
      arr.push({
        id: Helper.makeid(),
        order: i + 1,
        product_code: '',
        product_name: '',
        unit_code: null,
        unit_name: '',
        quantity: null,
        price: null,
        vat: null,
        amount_vat: null,
        amount: null,
        product_group: null,
        amount_after_vat: null
      });
    }
    return arr;
  }

  setConfig(value) {
    value.configs.forEach(item => {
      if (item.name === 'decimal_places_json') {
        this.formatter = VChanggeMoneyService.getFormatterColumnInv(JSON.parse(item.value));
      }
    });
    const obj1 = value.configs.filter(p => p.name === 'system_parameter')[0];
    if (obj1) {
      this.formatter.display_int = this.system_parameter.display_int;
    }
  }

  onNumberChange() {
    VChanggeMoneyService._changeMoney(this.frmData);
  }

  // // s kin form
  totalPaymentChange = (e) => {
    this.onNumberChange();
    let currency_code = this.frmData.currency_code;
    if (this.system_parameter.view_price_nte) {
      currency_code = 'VND';
    }
    //  this.frmData.total_payment_in_word = Helper.tienBangChu(e.value, VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);
  };

  getDataInChildForm(e) {
    this.frmData = e;
    this.frmTraLai.reason = e.lido;
    this.invoice_products = new ArrayStore({
      key: 'id',
      data: this.createDataSource(this.frmData.input_invoice_products)
    });
    this.dataSource = new DataSource({
      store: this.invoice_products
    });
    this.fileData = this.frmData.input_invoice_files;
    if (this.frmData.version_xml == '1') {
      this.thongtinBenBan(false);
      this.verifySignature();
    }
    else {
      this.noiDungFileHoaDon = "";
      this.noiDungNguoiBan = "";
    }
  }

  onTemplateChange(e) {
    this.is_multi_vat = e;
  }

  ngOnDestroy() {
    this.ioSignature.unsubscribe();
    // this.ioSignature.unsubscribe();
    // this.routeSub.unsubscribe();
    // this.socketService.close();
  }

  // canDeactivate(): Observable<boolean> | Promise<boolean> | boolean {
  //   if (!this.checkDataFormChange(this.valueChange, this.frmData)) {
  //     if (confirm(this._translate.CONFIRM.waring_save_data)) {
  //       return this.subData('GHI_TAM', this.frmData).then(value => value);
  //     }
  //     return true;
  //   }
  //   return true;
  // }

  _validForm() {
    const _lst = this.dataSource.items().filter(p => p.product_name);
    if (_lst.length > 0) {
      const sttItem = _lst.filter((item) => {
        if (!item.product_group) {
          return item;
        }
      });
      if (sttItem.length > 0) {
        Library.notify(this._translate.NOTIFY.no_product, 'error');
        this.productGrip.dataGrid.instance.focus(this.productGrip.dataGrid.instance.getCellElement(_lst.indexOf(sttItem[0]), 'product_group'));
        return false;
      }
    }
    return true;
  }

  eSubmit(e) {
    const checkValid = this.formInvoice.form.instance.validate();
    if (!checkValid.isValid) {
      return;
    }
    if (!this._validForm()) {
      return;
    }
    const valueDataSource = this.dataSource.items().filter((item) => {
      if (item.product_name && item.product_group) {
        return item;
      }
    });
    if (this.formatter)
      this.frmData.decimal_places_json = JSON.stringify(VChanggeMoneyService.getObjFormatterToColumn(this.formatter));
    this.frmData.is_import_from_zip = false;
    const dataForm = {
      input_invoice: this.frmData,
      input_invoice_products: valueDataSource,
      input_invoice_files: this.fileData,
    };
    for (const [key] of Object.entries(dataForm.input_invoice)) {
      if (key === 'id') {
        delete dataForm.input_invoice[key];
      }
      if (key === 'input_invoice_products') {
        delete dataForm.input_invoice[key];
      }
      if (key === 'input_invoice_files') {
        delete dataForm.input_invoice[key];
      }
    }
    dataForm.input_invoice_products.forEach(value => {
      for (const [key] of Object.entries(value)) {
        if (key === 'id') {
          delete value[key];
        }
      }
    });
    Library.showloading();
    if (this.idInvoice !== null) {
      this.invoiceBuyService.apiUpdateInvoice(this.idInvoice, dataForm).subscribe(
        value1 => {
          if (value1.status === 'success') {
            Library.notify(this._translate.NOTIFY.update_success, 'success');
          } else {
            Library.notify(value1.message, 'error');
          }
        },
        valueError => Library.notify(valueError, 'error'),
        () => Library.hideloading()
      );
    } else {
      this.invoiceBuyService.apiInvoice(dataForm).subscribe(
        value1 => {
          if (value1.status === 'success') {
            if (value1.data && value1.data.result)
              this.idInvoice = value1.data.result.id;
            Library.notify(this._translate.NOTIFY.update_success, 'success');
          } else {
            Library.notify(value1.message, 'error');
          }
        }, valueError => Library.notify(valueError, 'error'),
        () => Library.hideloading()
      );
    }
  }

  onTemplateTypeChange(e) {
    // this.invoicereleaseTempt = e;
  }

  keyDown(e: any) {
    if (e.event.key === 'Delete') {
      e.event.srcElement.value = null;
    }
  }
  verifySignature() {
    let fileContent = "";
    if (VStringService._getValueToString(this.frmData.version_xml) == "1") {
      let oFile = this.fileData.filter(p => VStringService._attemptConvertBool(p.is_file_import))[0];
      if (oFile)
        fileContent = oFile.file_content;
    }
    else {
      if (!this.frmData.json_data) {
        this.frmData.json_data =
        {
          statusCert: null,
          signDateCompareCert: null,
          signDateCompareInv: null,
          statusFileXml: null,
          capBoi: null,
          kyBoi: null,
          thoiHanChungThu: null,
          thoiGianKyHoaDon: null,
          tax_code: null,
          name: null,
          business_address: null,
          status: null,
          mstNB: null
        }
      }
      // 1. Ha n t ng c t efy
      if (VStringService._attemptConvertBool(this.frmData.export_from_ihd)) {
        if (this.frmData.xml_string)
          fileContent = this.frmData.xml_string;
      }
      // 2. Ha n trc tct
      else if (VStringService._attemptConvertBool(this.frmData.export_from_tct)) {
        if (this.frmData.xml_string) {
          fileContent = this.frmData.xml_string;
        }
      }
      // 3. Ha n trc nng cp
      else {
        let oFile = this.fileData.filter(p => VStringService._getValueToString(p.file_name) != "" && p.file_name == this.frmData.invoice_type_name)[0];
        if (oFile)
          fileContent = oFile.file_content;
      }
    }
    this.socketService.kiemtraCKS(() => this.connectWebsocket(), fileContent, this.frmData.signature_content, this.frmData.sign_date, this.frmData.invoice_date, this.frmData.seller_legal_name);
  }
  /* Kt ni n socket */
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      const data: any = message;
      if (data.SvcName === 'efy_verifySignature') {
        if (data.Status == "success") {
          this.frmData.json_data.statusCert = data.Data.statusCert;
          this.frmData.json_data.signDateCompareCert = data.Data.signDateCompareCert;
          this.frmData.json_data.signDateCompareInv = data.Data.signDateCompareInv;
          this.frmData.json_data.statusFileXml = data.Data.statusFileXml;
          this.frmData.json_data.capBoi = data.Data.capBoi;
          this.frmData.json_data.kyBoi = data.Data.kyBoi;
          this.frmData.json_data.thoiHanChungThu = data.Data.thoiHanChungThu;
          this.frmData.json_data.thoiGianKyHoaDon = data.Data.thoiGianKyHoaDon;
          this.frmData.signature_content = data.Data.SignatureSeller;
          this.fillDataToForm(data.Data);
        }
        else if (data.Status == "error") {
          Library.notify(data.Description, 'error');
        }
      }
    });
  }
  fillDataToForm(data) {
    let CTS_HopLe = "";
    let CTS_KhongHopLe = "";
    let NgayKyCTS_HopLe = "";
    let NgayKyCTS_KhongHopLe = "";
    let NgayKyNgayHoaDon_HopLe = "";
    let NgayKyNgayHoaDon_KhongHopLe = "";
    let KhongTonTaiNgayKy = "";
    let FileXML_HopLe = "";
    let FileXML_KhongHopLe = "";
    let TieuDeChiTiet = "";
    let CapBoi = "";
    let KyBoi = "";
    let thoiHanChungThu = "";
    let thoiGianKyHoaDon = "";
    /* Ch k s */
    if (VStringService._getValueToString(data.statusCert) == sEnum.notify.HopLe)
      CTS_HopLe = "<div class=\"font-success\"><span>Chng th s hp l</span></div>";
    else if (VStringService._getValueToString(data.statusCert) == sEnum.notify.KhongHopLe)
      CTS_KhongHopLe = "<div class=\"font-error\"><span>Chng th s khng hp l</span></div>";
    /* Kim tra Ngy k c tn ti k? */
    if (VStringService._getValueToString(data.signDateCompareCert) == sEnum.notify.KhongTonTai) {
      KhongTonTaiNgayKy = "<div class=\"font-error\"><span>Khng kim tra c thng tin ngy k</span></div>";
    }
    else {
      /* Ngy k nm trong thi hn hiu lc ca chng th s */
      if (VStringService._getValueToString(data.signDateCompareCert) == sEnum.notify.HopLe)
        NgayKyCTS_HopLe = "<div class=\"font-success\"><span>Ngy k nm trong thi hn hiu lc ca chng th s</span></div>";
      else if (VStringService._getValueToString(data.signDateCompareCert) == sEnum.notify.KhongHopLe)
        NgayKyCTS_KhongHopLe = "<div class=\"font-error\"><span>Ngy k khng nm trong thi hn hiu lc ca chng th s</span></div>";
      /* Ngy k trng vi ngy ha n  */
      if (VStringService._getValueToString(data.signDateCompareInv) == sEnum.notify.HopLe)
        NgayKyNgayHoaDon_HopLe = "<div class=\"font-success\"><span>Ngy k trng vi ngy ha n</span></div>";
      else if (VStringService._getValueToString(data.signDateCompareInv) == sEnum.notify.KhongHopLe)
        NgayKyNgayHoaDon_KhongHopLe = "<div class=\"font-error\"><span>Ngy k khng trng vi ngy ha n </span></div>";
    }
    /* File Xml */
    if (VStringService._getValueToString(data.statusFileXml) == sEnum.notify.HopLe)
      FileXML_HopLe = "<div class=\"font-success\"><span>File XML ha n cn nguyn vn</span></div>";
    else if (VStringService._getValueToString(data.statusFileXml) == sEnum.notify.KhongHopLe)
      FileXML_KhongHopLe = "<div class=\"font-error\"><span>File XML ha n  b chnh sa sau khi k</span></div>";
    if (data.kyBoi)
      TieuDeChiTiet = "<div class=\"font-success chi-tiet\"><span>Thng tin chi tit:</span></div>";
    if (data.capBoi)
      CapBoi = "<div class=\"font-success\"><span>CTS cp bi: </span><span>" + data.capBoi + "</span></div>";
    if (data.kyBoi)
      KyBoi = "<div class=\"font-success\"><span>K bi: </span><span>" + data.kyBoi + "</span></div>";
    if (data.thoiHanChungThu)
      thoiHanChungThu = "<div class=\"font-success\"><span>Thi hn CTS: </span><span>" + data.thoiHanChungThu + "</span></div>";
    if (data.thoiGianKyHoaDon)
      thoiGianKyHoaDon = "<div class=\"font-success\"><span>Thi gian k ha n: </span><span>" + data.thoiGianKyHoaDon + "</span></div>";

    this.noiDungFileHoaDon = CTS_HopLe + CTS_KhongHopLe + NgayKyCTS_HopLe + NgayKyCTS_KhongHopLe + NgayKyNgayHoaDon_HopLe + NgayKyNgayHoaDon_KhongHopLe +
      KhongTonTaiNgayKy + FileXML_HopLe + FileXML_KhongHopLe + TieuDeChiTiet + CapBoi + KyBoi + thoiHanChungThu + thoiGianKyHoaDon;
  }
  thongtinBenBan(isClickButton) {
    this.noiDungNguoiBan = "";

    if (VStringService._getValueToString(this.frmData.seller_tax_code) != "") {
      if (isClickButton)
        Library.showloading();
      //  this.customersService.getInfoCompany("0102519041").subscribe(
      this.customersService.getInfoCompany(this.frmData.seller_tax_code).subscribe(
        (res: any) => {
          let isErr = false;
          if (res.status === 'success') {
            if (res.data && res.data.customer) {
              if (res.data.customer.name) {
                let trangthaihoatdong = "";
                switch (res.data.customer.status) {
                  case "00":
                    trangthaihoatdong = "NNT ang hot ng";
                    break;
                  case "01":
                    trangthaihoatdong = "NNT ngng hot ng v  hon thnh th tc chm dt hiu lc MST";
                    break;
                  case "02":
                    trangthaihoatdong = "NNT  chuyn c quan thu qun l";
                    break;
                  case "03":
                    trangthaihoatdong = "NNT ngng H nhng cha hon thnh th tc chm dt hiu lc MST";
                    break;
                  case "04":
                    trangthaihoatdong = "NNT ang hot ng (cha y  th tc cp MST)";
                    break;
                  case "05":
                    trangthaihoatdong = "NNT tm ngng KD c thi hn";
                    break;
                  case "06":
                    trangthaihoatdong = "NNT khng hot ng ti a ch  ng k";
                    break;
                }
                res.data.customer.status = trangthaihoatdong;
                this.frmData.json_data.tax_code = res.data.customer.tax_code;
                this.frmData.json_data.name = res.data.customer.name;
                this.frmData.json_data.business_address = res.data.customer.business_address;
                this.frmData.json_data.status = trangthaihoatdong;
                this.frmData.json_data.mstNB = "success";
                this.viewThongTinBenBan(res.data.customer)
              }
              else
                isErr = true;
            }
            else
              isErr = true;
          } else
            isErr = true;
          if (isErr) {
            this.frmData.json_data.mstNB = "error";
            let line1 = "<div><p><span class=\"font-error\">H thng khng tm thy d liu. Kim tra li </span><a target=\"_blank\" class=\"linkTaiDay\" href=\"http://tracuunnt.gdt.gov.vn/tcnnt/mstdn.jsp\">Ti y</a></p></div>";
            this.noiDungNguoiBan = line1;
          }
          if (isClickButton)
            Library.hideloading();
        },
        error => {
          this.frmData.json_data.mstNB = "error";
          let line1 = "<div><p><span class=\"font-error\">H thng khng tm thy d liu. Kim tra li </span><a target=\"_blank\" class=\"linkTaiDay\" href=\"http://tracuunnt.gdt.gov.vn/tcnnt/mstdn.jsp\">Ti y</a></p></div>";
          this.noiDungNguoiBan = line1;
          if (isClickButton)
            Library.hideloading();
        }
      );
    }
    else {
      let line1 = "<div class=\"font-error\"><p>M s thu khng c  trng</p></div>";
      this.noiDungNguoiBan = line1;
    }
  }
  viewThongTinBenBan(data) {
    let mst = "";
    let tendonvi = "";
    let diachi = "";
    let trangthai = "";
    let khongtimthayNB = "";
    if (data.tax_code)
      mst = "<div class=\"font-success\"><span>M s thu: </span><span>" + data.tax_code + "</span></div>";
    if (data.name)
      tendonvi = "<div class=\"font-success\"><span>Tn n v: </span><span>" + data.name + "</span></div>";
    if (data.business_address)
      diachi = "<div class=\"font-success\"><span>a ch: </span><span>" + data.business_address + "</span></div>";
    if (data.status)
      trangthai = "<div class=\"font-success\"><span>Trng thi: </span><span>" + data.status + "</span></div>";
    if (data.mstNB == "error")
      khongtimthayNB = "<div><p><span class=\"font-error\">H thng khng tm thy d liu. Kim tra li </span><a target=\"_blank\" class=\"linkTaiDay\" href=\"http://tracuunnt.gdt.gov.vn/tcnnt/mstdn.jsp\">Ti y</a></p></div>";

    this.noiDungNguoiBan = mst + tendonvi + diachi + trangthai + khongtimthayNB;
  }
  onClickBenBan() {
    this.thongtinBenBan(true);
  }
  onClickChungThuSo() {
    this.verifySignature();
  }
  /* Thay i loi ha n */
  changedTypeInvoice(data) {
    this.frmData.xml_type = data.itemData.code;
  }
}
