import { Component, Input, OnInit, OnChanges, ViewEncapsulation, Output, EventEmitter, SimpleChanges, Renderer2, ViewChild, ElementRef, AfterViewInit, DoCheck, Pipe, PipeTransform } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Helper } from '@core/index';
import { Library } from '@core/library';
import { AuthService } from '@core/services';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { TranslateService } from '@ngx-translate/core';
import { ViewInvoiceMultipageComponent } from '@shared/components';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';


@Component({
  selector: 'app-preview-mail',
  templateUrl: './preview-mail.component.html',
  styleUrls: ['./preview-mail.component.scss'],
})
export class PreviewMailComponent implements OnChanges, OnInit {
  objParams: any
  exportSchedules: any;
  clickCurent: any;
  _translate: any;
  opened = false;
  html111 = ``
  iframe: any;
  stringFile: any;
  isShow: any;
  file: any;
  html: any;
  mst: any;
  filter: any;
  bsModalRef: BsModalRef;
  datasAccount: any;
  htmlResult: any;
  isMenuOpen = false;
  a: any;
  _language: any;
  @Input() isMailReceived;
  @Input() previewEmmail;
  @Output("myDropdown") myDropdown = new EventEmitter<any>();
  @ViewChild('toggleButton') toggleButton: ElementRef;
  @ViewChild('menu') menu: ElementRef;
  @ViewChild('menuDelete') menuDelete: ElementRef;
  constructor(private authService: AuthService,
    private invoiceBuyService: InvoiceBuyService,
    public translate: TranslateService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    private renderer: Renderer2,
    private domSanitizer: DomSanitizer
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
  }
  ngOnInit() {

  }
  ngOnChanges() {
    if (this.previewEmmail.attachments !== null) {
      this.file = JSON.parse(this.previewEmmail.attachments);
      this.handleFileImg();
    } else {
      this.file = [];
    }
    this.html = this.b64DecodeUnicode(this.previewEmmail.html_body)
    let text = this.html.split('body {width:795px;')
    let text2 = this.html.split('{width:795px;')
    if (text.length > 1) {
      let html = this.html.replace('body {width:795px;font-family:\'Times New Roman\', Times, serif;', 'body {width:100%;');
      let html2 = html.replace('serif;color: #000;', '');
      let html3 = html2.replace('margin-left: 29px;', 'margin-left: 0px;')
      let html4 = html3.replace('margin-left: 40px;', 'margin-left: 0px;')
      let html5 = html4.replace('font-family:Times New Roman,Times,serif;', '')
      this.htmlResult = html5
    } else if (text2.length > 1) {
      let html = this.html.replace('{margin-left:29px}', '');
      let html2 = html.replace('color:#000}', '');
      let html3 = html2.replace('{margin-left:40px}', '');
      let html4 = html3.replace('{width:795px;', '');
      let html5 = html4.replace('font-family:Times New Roman,Times,serif;', '')
      this.htmlResult = html5

    }
    else {
      this.htmlResult = this.html
    }
    this.renderer.listen('window', 'click', (e: Event) => {
      if (this.isMailReceived.listDelete) {
        if (!this.toggleButton.nativeElement.contains(e.target) && !this.menu.nativeElement.contains(e.target)) {
          this.isShow = ''
        }
      } else {
        if (!this.toggleButton.nativeElement.contains(e.target) && !this.menuDelete.nativeElement.contains(e.target)) {
          this.isShow = ''
        }
      }

    });
  }

  handleFileImg() {
    for (let i = 0; i < this.file.length; i++) {
      let fileNameArray = this.file[i].file_name.split('.')
      let fileType = fileNameArray[fileNameArray.length - 1]
      if (fileType == 'pdf') {
        this.file[i].classImg = fileType
      } else if (fileType == "docx") {
        this.file[i].classImg = fileType
      } else if (fileType == "html") {
        this.file[i].classImg = fileType
      } else if (fileType == "xml") {
        this.file[i].classImg = fileType
      } else if (fileType == "rar") {
        this.file[i].classImg = fileType
      } else if (fileType == "zip") {
        this.file[i].classImg = fileType
      } else {
        this.file[i].classImg = 'img'
      }
    }
  }
  onExportSchedules() {

  }
  b64DecodeUnicode(str) {
    // Going backwards: from bytestream, to percent-encoding, to original string.
    return decodeURIComponent(atob(str).split('').map(function (c) {
      return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
    }).join(''));
  }
  open() {
    this.opened = !this.opened
  }
  myFunction() {
    this.isMenuOpen = !this.isMenuOpen;
    if (this.isMenuOpen) {
      this.isShow = 'show'
    }
    else {
      this.isShow = ''
    }
  }
  deleteMail() {
    this.isShow = ''
    let id = this.previewEmmail.id
    let arrId = [];
    let ojb = {};
    arrId.push(id)
    if (this.isMailReceived.listDelete) {
      ojb = {
        "soft_del": true,
        "ids": arrId
      }
    } else {
      ojb = {
        "soft_del": false,
        "ids": arrId
      }
    }
    const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.invoiceBuyService.deleteMailId(ojb).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify(this._translate.NOTIFY.delete_success, 'success');
            let emitData = { idMail: id, isTrue: true, action: 'xoa_mail' }
            this.myDropdown.emit(emitData);
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }

  noReceived() {
    this.isShow = ''
    let id = this.previewEmmail.id
    let arrId = [];
    arrId.push(id)
    let obj = {
      ids: arrId,
      error_reason: "Ngi dng  chuyn trng thi email thnh khng hp l",
    }
    const result = Library.confirm(this._translate.CONFIRM.definitely_do_not_receive, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.invoiceBuyService.notReceived(obj).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify(this._translate.NOTIFY.finish, 'success');
            let emitData = { idMail: id, isTrue: true, action: 'khong_nhan_hd' }
            this.myDropdown.emit(emitData);
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }
  restore() {
    this.isShow = ''
    let id = this.previewEmmail.id
    let arrId = [];
    arrId.push(id)
    const result = Library.confirm(this._translate.CONFIRM.restore, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.invoiceBuyService.restore(arrId).subscribe(rs => {
          if (rs.status === 'success') {
            Library.notify(this._translate.NOTIFY.restore, 'success');
            let emitData = { idMail: id, isTrue: true, action: 'khoi_phuc' }
            this.myDropdown.emit(emitData);
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }
  nhanHoaDon() {
    this.isShow = ''
    const result = Library.confirm(this._translate.CONFIRM.would_you_like_to_receive_this_invoice, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        let ids = [];
        ids.push(this.previewEmmail.id);
        let isKhongNhan = false;
        let lstThuHopLe = this.previewEmmail.status
        if (lstThuHopLe == 'HOP_LE')
          isKhongNhan = true;
        if (isKhongNhan) {
          Library.notify(this._translate.NOTIFY.you_cannot_do_this_for_valid_email, 'error');
        }
        else {
          Library.showloading();
          this.invoiceBuyService.nhanHoaDon(ids).subscribe(res => {
            if (res.status == 'success') {
              Library.notify(this._translate.NOTIFY.receive_invoice_success, 'success');
              let emitData = { idMail: this.previewEmmail.id, isTrue: true, action: 'nhan_hd' }
              this.myDropdown.emit(emitData);
            } else {
              Library.notify(res.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      }
    });
  }
  getInforAccount() {
    const dataAcount = JSON.parse(window.localStorage.getItem('currentUser'));
    this.datasAccount = dataAcount.account;
  }
  getInforFile(index) {
    let id = this.file[index].file_id;
    let fileArray = this.file[index].file_name.split(".")
    let fileType = fileArray[fileArray.length - 1]
    let fileName = '';
    for (let i = 0; i < fileArray.length - 1; i++) {
      fileName += fileArray[i]
    }
    this.invoiceBuyService.getInfoFile(id).subscribe(res => {
      if (res.status == 'success') {
        if (fileType == "xml") {
          Helper.showFileXml(fileName, fileType, res.data.file_content)
        }
        else if (fileType == "html" || fileType == "pdf" || fileType == "png" || fileType == "jpeg" || fileType == "jpg") {
          const initialState = {
            fileName: fileName,
            fileType: fileType,
            fileContent: res.data.file_content,
            typeAction: 'XEM_FILE'
          };
          this.bsModalRef = this.modalImportService.show
            (
              ViewInvoiceMultipageComponent,
              {
                backdrop: 'static',
                keyboard: false,
                class: 'modal-xl view-invoice',
                initialState
              }
            );
          const subModal = this.modalImportService.onHide.subscribe(() => {
            subModal.unsubscribe();
          });
        } else {
          Helper.dowloadFile(fileName, fileType, res.data.file_content, true);
        }
      }
    })
  }
}
@Pipe({ name: "safeHtml" })
export class SafeHtmlPipe implements PipeTransform {
  constructor(private sanitizer: DomSanitizer) { }

  transform(value) {
    return this.sanitizer.bypassSecurityTrustHtml(value);
  }
}
