import { Component, OnInit } from '@angular/core';
import { AuthService } from '@core/services';
import { TranslateService } from '@ngx-translate/core';
import { BsModalRef } from 'ngx-bootstrap';
import xml2js from 'xml2js';
import * as moment from 'moment';
import { Invoice } from '@modules/invoice-buy/shared/class/invoice.model';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { Library } from '@core/library';
import { Helper } from '@core/helper/Helper';
import { sEnum, VChanggeMoneyService } from '@core/index';

@Component({
  selector: 'app-upload-file-zip',
  templateUrl: './upload-file-zip.component.html',
  styleUrls: ['./upload-file-zip.component.scss']
})
export class UploadFileZipComponent implements OnInit {
  _translate: any;
  _language: string;
  checkSubmit = false;
  checkSubmitBtn = false;
  arrData = [];
  arrDataBeforeSubmit = [];
  frmData: Invoice = new Invoice('1');
  totalArray = '';
  checkUpdateLoadList = false;
  widthPrecent = '0';
  showWidthPrecent = '0';
  totalPrecent: number;
  arrDataDone = [];
  arrDataError = [];
  isShowPrecent = false;
  isShowAllResult = false;
  totalArraySubmit: number;
  tax_code_local: string = null;
  isCollapsed = false;
  totalArrayFile = [];
  formatter: any;
  system_parameter: any = {};
  configs: any;
  lstFileDinhKem = [];

  constructor(
    private authService: AuthService,
    public translate: TranslateService,
    public bsModalRef: BsModalRef,
    private invoiceBuyService: InvoiceBuyService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
  }

  ngOnInit() {
    const dataUser = JSON.parse(localStorage.getItem('currentUser'));
    this.tax_code_local = dataUser.account.tax_code;
    this.configs = this.authService.getConfigs();
  }

  submit() {
    if (!this.arrData.length) {
      Library.notify(this._translate.VALIDATION.file_invoice_empty, 'error');
      return;
    }
    for (let i = 0; i < this.arrData.length; i++) {
      this.arrDataBeforeSubmit.push(this.parseXMLtoObject(this.arrData[i].data, this.arrData[i].name));
    }
    this.checkObjectNull(this.arrDataBeforeSubmit);
  }

  close() {
    if (this.checkUpdateLoadList) {
      this.bsModalRef.content.LoadList = 'LoadList';
    }
    this.bsModalRef.hide();
  }

  clickChooseFile() {
    document.getElementById('id-upload-file').click();
  }

  fileChanged($event) {
    const promises = [];
    const arrXML = $event.target.files;
    this.arrData = [];
    this.lstFileDinhKem = [];
    Library.showloading();
    for (let i = 0; i < arrXML.length; i++) {
      const filePromise = new Promise(resolve => {
        const reader = new FileReader();
        reader.readAsText(arrXML[i]);
        reader.onload = () => resolve({ name: arrXML[i].name, data: reader.result });
      });
      promises.push(filePromise);
    }
    Promise.all(promises).then(fileContents => {
      Library.hideloading();
      if (fileContents && fileContents.length > 0) {
        this.arrData = fileContents.filter(p => p.name.includes('.xml'));
        this.lstFileDinhKem = fileContents.filter(p => !p.name.includes('.xml'));
        this.totalArrayFile = this.arrData;
        this.totalArray = (this.arrData.length).toString() + ' files c ti ln!';
        this.isShowAllResult = false;
        this.checkSubmitBtn = false;
        this.arrDataError = [];
        this.arrDataBeforeSubmit = [];
      }
    });
  }

  checkObjectNull(value) {
    const arrData = [];
    const arrDataErr = [];
    for (let i = 0; i < value.length; i++) {
      if (value[i].input_invoice_files) {
        arrData.push(value[i]);
      } else {
        arrDataErr.push({
          invoice_series: null,
          template_code: null,
          invoice_number: null,
          name: value[i].name,
          errorMess: 'File ha n khng tn ti',
        });
      }
    }
    this.checkDuplicate(arrData, arrDataErr);
    // this.handleDataSubmit(arrData, arrDataErr);
  }

  checkDuplicate(value, arrDataErr) {
    const arrData = value.reduce(function (accumulator, current) {
      if (checkIfAlreadyExist(current)) {
        arrDataErr.push({
          invoice_series: current.input_invoice.invoice_series,
          template_code: current.input_invoice.template_code,
          invoice_number: current.input_invoice.invoice_number,
          name: current.input_invoice_files[0].file_name,
          errorMess: 'File ha n b trng',
        });
        return accumulator;
      } else {
        // add item to array
        return accumulator.concat([current]);
      }

      function checkIfAlreadyExist(currentVal) {
        return accumulator.some(function (item) {
          return (item.input_invoice.invoice_series === currentVal.input_invoice.invoice_series &&
            item.input_invoice.invoice_number === currentVal.input_invoice.invoice_number &&
            item.input_invoice.template_code === currentVal.input_invoice.template_code);
        });
      }
    }, []);
    this.handleDataSubmit(arrData, arrDataErr);
  }

  handleDataSubmit(value, arrDataErr) {
    value.map((item) => {
      if (!item.input_invoice.decimal_places_json) {
        if (item.input_invoice.currency_rate) {
          item.input_invoice.currency_rate = item.input_invoice.currency_rate.replace(',', '.');
        }
        item.input_invoice_products.map((valueOfItem) => {
          if (valueOfItem.quantity) {
            valueOfItem.quantity = parseInt(valueOfItem.quantity.replace(',', '.'), 10);
          } else {
            valueOfItem.quantity = null;
          }
          if (valueOfItem.price) {
            valueOfItem.price = parseInt(valueOfItem.price.replace(',', '.'), 10);
          } else {
            valueOfItem.price = null;
          }
          if (valueOfItem.amount) {
            valueOfItem.amount = parseInt(valueOfItem.amount.replace(',', '.'), 10);
          } else {
            valueOfItem.amount = null;
          }
          if (valueOfItem.vat) {
            valueOfItem.vat = parseInt(valueOfItem.vat.replace(',', '.'), 10);
          } else {
            valueOfItem.vat = null;
          }
          if (valueOfItem.amount_vat) {
            valueOfItem.amount_vat = parseInt(valueOfItem.amount_vat.replace(',', '.'), 10);
          } else {
            valueOfItem.amount_vat = null;
          }
          if (valueOfItem.amount && valueOfItem.amount_vat) {
            valueOfItem.amount_after_vat = valueOfItem.amount + valueOfItem.amount_vat;
          } else if (valueOfItem.amount && !valueOfItem.amount_vat) {
            valueOfItem.amount_after_vat = valueOfItem.amount;
          } else {
            valueOfItem.amount_after_vat = null;
          }
          if (valueOfItem.unit_code === undefined) {
            valueOfItem.unit_code = null;
          }
        });
      }
    });
    // validate
    this.validArrItem(value, arrDataErr);
  }

  validArrItem(value, arrDataErr) {
    const arrData = [];
    if (Array.isArray(value)) {
      value.forEach((item) => {
        if (item.input_invoice.seller_tax_code && item.input_invoice.invoice_number && item.input_invoice.template_code && item.input_invoice.invoice_date && item.input_invoice.invoice_series) {
          arrData.push(item);
        } else {
          arrDataErr.push({
            invoice_series: item.input_invoice.invoice_series,
            template_code: item.input_invoice.template_code,
            invoice_number: item.input_invoice.invoice_number,
            name: item.input_invoice_files[0].file_name,
            errorMess: this._translate.VALIDATION.some_information_empty
          });
        }
      }
      );
      this.checkTaxCodeFile(arrData, arrDataErr);
    }
  }

  checkTaxCodeFile(value, arrDataErr) {
    const arrData = [];
    if (Array.isArray(value)) {
      value.forEach((item) => {
        if (item.input_invoice.buyer_tax_code === this.tax_code_local) {
          arrData.push(item);
        } else {
          arrDataErr.push(
            {
              invoice_series: item.input_invoice.invoice_series,
              template_code: item.input_invoice.template_code,
              invoice_number: item.input_invoice.invoice_number,
              name: item.input_invoice_files[0].file_name,
              errorMess: this._translate.VALIDATION.do_not_tax_code_unit
            });
        }
      });
      if (arrData.length > 0) {
        // this.submitData(arrData, arrDataErr);
        this.handleDataPostPromiseAll(arrData, arrDataErr);
      } else {
        Library.notify('Tt c ha n khng ng m s thu ca n v!', 'error');
      }
    }
  }

  handleDataPostPromiseAll(value, arrDataErr) {
    const arrDataPostAPi = [];
    while (value.length > 0) {
      arrDataPostAPi.push(value.splice(0, 3));
    }
    this.submitDataProMise(arrDataPostAPi, arrDataErr);
  }

  async submitDataProMise(value, arrDataErr) {
    const arrDone = [];
    Library.showloading();
    this.checkSubmit = true;
    this.checkSubmitBtn = true;
    this.isShowPrecent = true;
    this.totalPrecent = 100 / value.length;
    this.totalArraySubmit = value.length;
    for (let i = 0; i < value.length; i++) {
      const j = i + 1;
      this.widthPrecent = (this.totalPrecent * j).toString();
      this.showWidthPrecent = ((this.totalPrecent * j).toFixed(0)).toString();
      await Promise.all(
        value[i].map(
          item => this.invoiceBuyService.apiInvoice(item).toPromise().then(
            dataItem => {
              return {
                status: dataItem.status,
                message: dataItem.message,
                invoice_series: item.input_invoice.invoice_series,
                template_code: item.input_invoice.template_code,
                invoice_number: item.input_invoice.invoice_number,
                name: item.input_invoice_files[0].file_name,
              };
            }
          )))
        .then(
          (result: Array<any>) => {
            result.forEach((valueItem) => {
              if (valueItem.status === 'success') {
                arrDone.push(valueItem.name);
              } else {
                arrDataErr.push(
                  {
                    invoice_series: valueItem.invoice_series,
                    template_code: valueItem.template_code,
                    invoice_number: valueItem.invoice_number,
                    name: valueItem.name,
                    errorMess: valueItem.message
                  }
                );
              }
            });
          }
        );
    }
    Library.hideloading();
    Library.notify(this._translate.NOTIFY.add_invoice_complete, 'success');
    this.arrDataDone = arrDone;
    this.arrDataError = arrDataErr;
    this.isShowPrecent = false;
    this.checkUpdateLoadList = true;
    this.checkSubmit = false;
    this.widthPrecent = '0';
    this.showWidthPrecent = '0';
    this.isShowAllResult = true;
  }

  parseXMLtoObject(value, nameFile) {
    let a;
    const dataBase64Code = window.btoa(unescape(encodeURIComponent(value)));
    // paser to base64 to a string
    // const b = decodeURIComponent(escape(window.atob(a)));
    xml2js.parseString(value, (err, result) => {
      if (result) {
        if (result.hasOwnProperty('Invoice')) {
          let X509Certificate = '';
          if (result['Invoice']['Signature'])
            X509Certificate = result['Invoice']['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0]
          a = this.getValueToObject('dreamSoft', result['Invoice']['Content'][0], nameFile, dataBase64Code, X509Certificate);
        } else if (result.hasOwnProperty('inv:invoice')) {
          let X509Certificate = '';
          if (result['inv:invoice']['Signature'])
            X509Certificate = result['inv:invoice']['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0];
          a = this.getValueToObject('all', result['inv:invoice']['inv:invoiceData'][0], nameFile, dataBase64Code, X509Certificate);
        } else if (result.hasOwnProperty('inv:transaction')) {
          let X509Certificate = '';
          if (result['inv:transaction']['inv:invoices'][0]['inv:invoice'][0]['Signature'])
            X509Certificate = result['inv:transaction']['inv:invoices'][0]['inv:invoice'][0]['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0];
          a = this.getValueToObject('all', result['inv:transaction']['inv:invoices'][0]['inv:invoice'][0]['inv:invoiceData'][0], nameFile, dataBase64Code, X509Certificate);
        } else if (result.hasOwnProperty("HDon")) {
          a = this.readDataFromXmlTCT(result, nameFile, dataBase64Code);
        } else {
          a = this.returnObjectError(nameFile);
        }
      } else {
        a = this.returnObjectError(nameFile);
      }
    });
    return a;
  }

  returnObjectError(fileName) {
    return {
      name: fileName,
      input_invoice_files: null
    };
  }

  getValueToObject(type, value, nameFile, dataBase64Code, signature) {
    const object = {};
    for (const property in value) {
      if (value.hasOwnProperty(property)) {
        if (typeof value[property][0] === 'string') {
          object[property] = value[property][0].trim();
        } else {
          object[property] = value[property][0];
        }
      }
    }
    if (type === 'dreamSoft') {
      return this.addValueToFormFormat1(object, nameFile, dataBase64Code, signature);
    } else if (type === 'all') {
      return this.addValueToFormFormat2(object, nameFile, dataBase64Code, signature);
    }
  }

  // done
  addValueToFormFormat1(object, nameFile, dataBase64Code, signature) {
    const invoice_type_name = nameFile;
    let signature_content = signature;
    let invoice_date = "";
    let sign_date = "";
    if (object['ArisingDate'])
      invoice_date = moment(object['ArisingDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
    if (object['SignDate'])
      sign_date = moment(object['SignDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
    const template_code = object['InvoicePattern'];
    let payment_method_view_name;
    if (object.hasOwnProperty('Kind_of_Payment')) {
      payment_method_view_name = object['Kind_of_Payment'];
    } else {
      payment_method_view_name = object['PaymentMethod'];
    }
    const invoice_series = object['SerialNo'];
    const seller_legal_name = object['ComName'];
    const invoice_number = object['InvoiceNo'];
    const currency_code = object['CurrencyUnit'];
    const currency_rate = object['ExchangeRate'];
    const adjustment_type = '1';
    const seller_tax_code = object['ComTaxCode'];
    const seller_address = object['ComAddress'];
    const seller_mobile = object['ComPhone'];
    let seller_email;
    if (object['ComEmail']) {
      if (object['ComEmail'] !== '.') {
        seller_email = object['ComEmail'];
      } else {
        seller_email = null;
      }
    } else {
      seller_email = null;
    }
    let seller_fax_number;
    if (object.hasOwnProperty('ComFaxCode')) {
      seller_fax_number = object['ComFaxCode'];
    }
    const buyer_legal_name = object['CusName'];
    const buyer_tax_code = object['CusTaxCode'];
    const buyer_mobile = object['CusPhone'];
    const buyer_address = object['CusPhone'];
    const buyer_email = object['CusEmail'];
    let seller_bank_account;
    if (object.hasOwnProperty('ComBankNo')) {
      if (object['ComBankName']) {
        seller_bank_account = object['ComBankNo'] + ' - ' + object['ComBankName'];
      } else {
        seller_bank_account = object['ComBankName'];
      }
    } else {
      seller_bank_account = object['ComBankName'];
    }
    const amount = parseInt(object['Total'].replace(',', '.'), 10);
    let total_amount_vat;
    if (object.hasOwnProperty('VATAmount')) {
      total_amount_vat = parseInt(object['VATAmount'].replace(',', '.'), 10);
    } else {
      total_amount_vat = parseInt(object['VAT_Amount'].replace(',', '.'), 10);
    }
    const total_payment = parseInt(object['Amount'].replace(',', '.'), 10);
    let total_payment_in_word;
    if (object.hasOwnProperty('Amount_words')) {
      total_payment_in_word = object['Amount_words'];
    } else {
      total_payment_in_word = object['AmountInWords'];
    }
    let lstFile = [];
    lstFile.push({
      order: 'DK',
      file_name: nameFile,
      file_content: dataBase64Code,
      is_file_import: true
    });
    if (this.lstFileDinhKem && this.lstFileDinhKem.length > 0) {
      let tenFileHD = nameFile.split('.')[0];
      this.lstFileDinhKem.forEach(file => {
        let tenFileDK = file.name.split('.')[0];
        if (tenFileDK == tenFileHD) {
          let endCodeDK = window.btoa(unescape(encodeURIComponent(file.data)));
          let fileDK = {
            order: 'BK',
            file_name: file.name,
            file_content: endCodeDK,
          }
          lstFile.push(fileDK);
        }
      });
    }
    const input_invoice_files = lstFile;
    let input_invoice_products;
    if (object['Products']) {
      const arrItem = object['Products']['Product'];
      for (let i = 0; i < arrItem.length; i++) {
        for (const keyItem in arrItem[i]) {
          if (arrItem[i].hasOwnProperty(keyItem)) {
            arrItem[i][keyItem] = arrItem[i][keyItem][0].trim();
          }
        }
      }
      input_invoice_products = this.invoiceBuyService.formatArrayItem(arrItem);
    }
    return {
      input_invoice: {
        total_payment_in_word: total_payment_in_word,
        total_payment: total_payment,
        total_amount_vat: total_amount_vat,
        amount: amount,
        seller_bank_account: seller_bank_account,
        buyer_email: buyer_email,
        buyer_address: buyer_address,
        buyer_mobile: buyer_mobile,
        buyer_tax_code: buyer_tax_code,
        buyer_legal_name: buyer_legal_name,
        seller_fax_number: seller_fax_number,
        seller_mobile: seller_mobile,
        seller_email: seller_email,
        invoice_series: invoice_series,
        seller_legal_name: seller_legal_name,
        invoice_number: invoice_number,
        currency_code: currency_code,
        currency_rate: currency_rate,
        adjustment_type: adjustment_type,
        seller_tax_code: seller_tax_code,
        seller_address: seller_address,
        payment_method_view_name: payment_method_view_name,
        template_code: template_code,
        invoice_type_name: invoice_type_name,
        invoice_date: invoice_date,
        origin_id: null,
        note: null,
        seller_contact_name: null,
        seller_surrogate_name: null,
        seller_tax_department: null,
        buyer_name: null,
        auto_value: false,
        auto_value_price: false,
        amount_vat5: null,
        amount_vat10: null,
        amount_vatx: null,
        amount_vat0: null,
        total_amount_product_vat5: null,
        total_amount_product_vat10: null,
        total_amount_product_vatx: null,
        total_amount_product_vat0: null,
        amount_discount: null,
        system_code: null,
        export_from_ihd: 0,
        sign_date: sign_date,
        signature_content: signature_content,
        version_xml: "1",
        xml_type: "2",
        is_import_from_zip: true,
        json_data: {
          statusCert: null,
          signDateCompareCert: null,
          signDateCompareInv: null,
          statusFileXml: null,
          capBoi: null,
          kyBoi: null,
          thoiHanChungThu: null,
          thoiGianKyHoaDon: null,
          tax_code: null,
          name: null,
          business_address: null,
          status: null,
          mstNB: null
        }
      },
      input_invoice_products: input_invoice_products,
      input_invoice_files: input_invoice_files
    };
  }
  /* c file xml t Tng Cc Thu */
  readDataFromXmlTCT(content, nameFile, dataBase64Code) {
    this.frmData = new Invoice('1');
    this.formatter = VChanggeMoneyService.getFormatterColumnInv(this.configs.decimal_places_json);
    let TTChung = content['HDon']['DLHDon'][0]['TTChung'][0];
    let NBan = content['HDon']['DLHDon'][0]['NDHDon'][0]['NBan'][0];
    let NMua = content['HDon']['DLHDon'][0]['NDHDon'][0]['NMua'][0];
    let DSHHDVu = content['HDon']['DLHDon'][0]['NDHDon'][0]['DSHHDVu'][0]['HHDVu'];
    let TToan = content['HDon']['DLHDon'][0]['NDHDon'][0]['TToan'][0];
    let X509Certificate = content['HDon']['DSCKS'][0]['NBan'][0]['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0];
    let THTTLTSuat = [];
    if (content['HDon']['DLHDon'][0]['NDHDon'][0]['TToan'][0]['THTTLTSuat'])
      THTTLTSuat = content['HDon']['DLHDon'][0]['NDHDon'][0]['TToan'][0]['THTTLTSuat'][0]['LTSuat'];

    let signDate = content['HDon']['DSCKS'][0]['NBan'][0]['Signature'][0]['Object'][0]['SignatureProperties'][0]['SignatureProperty'][0]['SigningTime'][0]._;
    let TTHDLQuan = content['HDon']['DLHDon'][0]['TTChung'][0]["TTHDLQuan"];
    if (TTHDLQuan) {
      let TCHDon = content['HDon']['DLHDon'][0]['TTChung'][0]["TTHDLQuan"][0]["TCHDon"];
      if (TCHDon) {
        TCHDon = content['HDon']['DLHDon'][0]['TTChung'][0]["TTHDLQuan"][0]["TCHDon"][0];
        if (TCHDon == "1")
          this.frmData.adjustment_type = "3";
        else if (TCHDon == "2")
          this.frmData.adjustment_type = "5";
      }
      else
        this.frmData.adjustment_type = "1";
    }
    else
      this.frmData.adjustment_type = "1";
    if (!signDate)
      signDate = content['HDon']['DSCKS'][0]['NBan'][0]['Signature'][0]['Object'][0]['SignatureProperties'][0]['SignatureProperty'][0]['SigningTime'][0];
    let MaCQT = "";
    if (content['HDon']['MCCQT'])
      MaCQT = content['HDon']['MCCQT'][0]._;
    this.frmData.tax_authority_code = MaCQT;
    this.frmData.invoice_type_name = nameFile;
    this.frmData.xml_type = "3";
    /* Thng tin chung */
    for (const property in TTChung) {
      let valueI = TTChung[property][0];
      switch (property) {
        case "KHMSHDon":
          this.frmData.template_code = valueI;
          break;
        case "KHHDon":
          this.frmData.invoice_series = valueI;
          break;
        case "SHDon":
          this.frmData.invoice_number = valueI;
          break;
        case "NLap":
          const date = new Date(valueI);
          if (date.toString() === 'Invalid Date') {
            this.frmData.invoice_date = moment(valueI, 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
          } else {
            this.frmData.invoice_date = moment(valueI).format('YYYY-MM-DD hh:mm:ss');
          }
          break;
        case "DVTTe":
          this.frmData.currency_code = valueI;
          break;
        case "HTTToan":
          this.frmData.payment_method_view_name = valueI;
          break;
      }
    }
    /* Thng tin bn bn */
    for (const property in NBan) {
      let valueI = NBan[property][0];
      switch (property) {
        case "Ten":
          this.frmData.seller_legal_name = valueI;
          break;
        case "MST":
          this.frmData.seller_tax_code = valueI;
          break;
        case "DChi":
          this.frmData.seller_address = valueI;
          break;
        case "SDThoai":
          this.frmData.seller_mobile = valueI;
          break;
      }
    }
    /* Thng tin bn mua */
    for (const property in NMua) {
      let valueI = NMua[property][0];
      switch (property) {
        case "Ten":
          this.frmData.buyer_legal_name = valueI;
          break;
        case "MST":
          this.frmData.buyer_tax_code = valueI;
          break;
        case "DChi":
          this.frmData.buyer_address = valueI;
          break;
        case "SDThoai":
          this.frmData.buyer_mobile = valueI;
          break;
        case "MKHang":
          this.frmData.customer_code = valueI;
          break;
        case "DCTDTu":
          this.frmData.buyer_email = valueI;
          break;
          case "HVTNMHang":
          this.frmData.buyer_name = valueI;
          break;
      }
    }
    /* Thng tin S tin trn ha n */
    for (const property in TToan) {
      let valueI = TToan[property][0];
      switch (property) {
        case "TgTCThue":
          this.frmData.amount = valueI;
          break;
        case "TgTThue":
          this.frmData.total_amount_vat = valueI;
          break;
        case "TgTTTBSo":
          this.frmData.total_payment = valueI;
          break;
        case "TgTTTBChu":
          this.frmData.total_payment_in_word = valueI;
          break;
      }
    }
    if (THTTLTSuat && THTTLTSuat.length > 0) {
      let lstTThue = [];
      for (let i = 0; i < THTTLTSuat.length; i++) {
        const object = {};
        for (const property in THTTLTSuat[i]) {
          let valueI = THTTLTSuat[i][property][0];
          object[property] = valueI;
        }
        lstTThue.push(object);
      }
      if (lstTThue.length > 0) {
        lstTThue.forEach(itemT => {
          switch (itemT.TSuat) {
            case "0%":
              this.frmData.total_amount_product_vat0 = itemT.ThTien;
              break;
            case "5%":
              this.frmData.total_amount_product_vat5 = itemT.ThTien;
              this.frmData.amount_vat5 = itemT.TThue;
              break;
            case "8%":
              this.frmData.total_amount_product_vat8 = itemT.ThTien;
              this.frmData.amount_vat8 = itemT.TThue;
              break;
            case "10%":
              this.frmData.total_amount_product_vat10 = itemT.ThTien;
              this.frmData.amount_vat10 = itemT.TThue;
              break;
            case "KCT":
              this.frmData.total_amount_product_vatx = itemT.ThTien;
              break;
            case "KKKNT":
              this.frmData.total_amount_product_vat_kkknt = itemT.ThTien;
              break;
          }
        });
      }
    }
    /* Danh sch hng ha, dch v */
    if (DSHHDVu && DSHHDVu.length > 0) {
      let lstHangHoa = [];
      for (let i = 0; i < DSHHDVu.length; i++) {
        const object = {};
        for (const property in DSHHDVu[i]) {
          let valueI = DSHHDVu[i][property][0];
          object[property] = valueI;
        }
        lstHangHoa.push(object);
      }
      if (lstHangHoa.length > 0) {
        this.frmData.input_invoice_products = [];
        lstHangHoa.forEach(itemHH => {
          let oHangHoa = {};
          oHangHoa["order"] = itemHH.STT ? itemHH.STT : null;
          oHangHoa["product_code"] = itemHH.MHHDVu ? itemHH.MHHDVu : null;
          oHangHoa["product_name"] = itemHH.THHDVu ? itemHH.THHDVu : null;
          oHangHoa["unit_name"] = itemHH.DVTinh ? itemHH.DVTinh : null;
          oHangHoa["quantity"] = itemHH.SLuong;
          oHangHoa["price"] = itemHH.DGia;
          oHangHoa["amount"] = itemHH.ThTien;
          oHangHoa["product_group"] = "1";
          oHangHoa["unit_code"] = null;
          oHangHoa["is_promotion"] = null;
          switch (itemHH.TSuat) {
            case "0%":
              oHangHoa["vat"] = 0;
              break;
            case "5%":
              oHangHoa["vat"] = 5;
              break;
            case "10%":
              oHangHoa["vat"] = 10;
              break;
            case "8%":
              oHangHoa["vat"] = 8;
              break;
            case "KCT":
              oHangHoa["vat"] = -1;
              break;
            case "KKKNT":
              oHangHoa["vat"] = -2;
              break;
            default:
              oHangHoa["vat"] = null;
              break;
          }
          /* Cu hnh thp phn */
          //->S lng
          if (oHangHoa["quantity"]) {
            let arr = oHangHoa["quantity"].split('.');
            if (arr.length == 2) {
              this.formatter.so_luong = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.so_luong = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> n gi
          if (oHangHoa["price"]) {
            let arr = oHangHoa["price"].split('.');
            if (arr.length == 2) {
              this.formatter.don_gia = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.don_gia = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> Thnh tin
          if (oHangHoa["amount"]) {
            let arr = oHangHoa["amount"].split('.');
            if (arr.length == 2) {
              this.formatter.thanh_tien = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.thanh_tien = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> Tin thu
          if (this.frmData.total_amount_vat) {
            let arr = this.frmData.total_amount_vat.split('.');
            if (arr.length == 2) {
              this.formatter.tien_thue = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.tien_thue = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> Tng tin
          if (this.frmData.total_payment) {
            let arr = this.frmData.total_payment.split('.');
            if (arr.length == 2) {
              this.formatter.tong_tien = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.tong_tien = VChanggeMoneyService.formatDecimalColumn(0);
          }

          if (oHangHoa["quantity"])
            oHangHoa["quantity"] = VChanggeMoneyService.roundNumber(Helper.toNumber(oHangHoa["quantity"]), this.formatter.so_luong);
          else
            oHangHoa["quantity"] = null;

          if (oHangHoa["price"])
            oHangHoa["price"] = VChanggeMoneyService.roundNumber(Helper.toNumber(oHangHoa["price"]), this.formatter.don_gia);
          else
            oHangHoa["price"] = null;

          if (oHangHoa["amount"])
            oHangHoa["amount"] = VChanggeMoneyService.roundNumber(Helper.toNumber(oHangHoa["amount"]), this.formatter.thanh_tien);
          else
            oHangHoa["amount"] = null;

          /* Tnh tin thu */
          if (oHangHoa["amount"] != null && oHangHoa["vat"] != null) {
            if (oHangHoa["vat"] > 0) {
              oHangHoa["amount_vat"] = VChanggeMoneyService.roundNumber((oHangHoa["amount"] * oHangHoa["vat"]) / 100, this.formatter.tien_thue);
            } else if (oHangHoa["vat"] == 0)
              oHangHoa["amount_vat"] = VChanggeMoneyService.roundNumber(0, this.formatter.tien_thue);
            else
              oHangHoa["amount_vat"] = null;
          } else if (oHangHoa["vat"] == null || (oHangHoa["vat"] != null && oHangHoa["amount"] == null))
            oHangHoa["amount_vat"] = null;
          /* Tnh tng tin hng ha */
          if (oHangHoa["amount_vat"] != null && oHangHoa["amount"] != null)
            oHangHoa["amount_after_vat"] = VChanggeMoneyService.roundNumber(oHangHoa["amount"] + oHangHoa["amount_vat"], this.formatter.tong_tien);
          else if (oHangHoa["amount"] != null)
            oHangHoa["amount_after_vat"] = VChanggeMoneyService.roundNumber(oHangHoa["amount"], this.formatter.tong_tien);
          else
            oHangHoa["amount_after_vat"] = null;

          this.frmData.input_invoice_products.push(oHangHoa);
        });
      }
    }

    /* Cu hnh thp phn cho s tin */
    if (this.frmData.total_amount_product_vat0)
      this.frmData.total_amount_product_vat0 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat0), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat0 = null;

    if (this.frmData.total_amount_product_vat5)
      this.frmData.total_amount_product_vat5 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat5), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat5 = null;

    if (this.frmData.total_amount_product_vat8)
      this.frmData.total_amount_product_vat8 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat8), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat8 = null;

    if (this.frmData.total_amount_product_vat10)
      this.frmData.total_amount_product_vat10 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat10), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat10 = null;

    if (this.frmData.total_amount_product_vatx)
      this.frmData.total_amount_product_vatx = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vatx), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vatx = null;

    if (this.frmData.total_amount_product_vat_kkknt)
      this.frmData.total_amount_product_vat_kkknt = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat_kkknt), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat_kkknt = null;

    if (this.frmData.amount)
      this.frmData.amount = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount), this.formatter.thanh_tien);
    else
      this.frmData.amount = null;

    if (this.frmData.total_amount_vat)
      this.frmData.total_amount_vat = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_vat), this.formatter.tien_thue);
    else
      this.frmData.total_amount_vat = null;

    if (this.frmData.total_payment)
      this.frmData.total_payment = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_payment), this.formatter.tong_tien);
    else
      this.frmData.total_payment = null;

    if (this.frmData.amount_vat5)
      this.frmData.amount_vat5 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount_vat5), this.formatter.tien_thue);
    else
      this.frmData.amount_vat5 = null;

    if (this.frmData.amount_vat8)
      this.frmData.amount_vat8 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount_vat8), this.formatter.tien_thue);
    else
      this.frmData.amount_vat8 = null;

    if (this.frmData.amount_vat10)
      this.frmData.amount_vat10 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount_vat10), this.formatter.tien_thue);
    else
      this.frmData.amount_vat10 = null;

    this.frmData.input_invoice_products = this.frmData.input_invoice_products.filter((item) => {
      if (item.product_name !== 'Tng tin hng' && item.product_name !== 'Tng tin chit khu' && item.product_name !== 'Chit khu') {
        return item;
      }
    });
    this.frmData.version_xml = "1";
    this.frmData.is_import_from_zip = true;
    this.frmData.signature_content = X509Certificate;
    if (signDate)
      this.frmData.sign_date = signDate.replace("T", " ");
    let lstFile = [];
    lstFile.push({
      order: 'DK',
      file_name: nameFile,
      file_content: dataBase64Code,
      is_file_import: true
    });
    if (this.lstFileDinhKem && this.lstFileDinhKem.length > 0) {
      let tenFileHD = nameFile.split('.')[0];
      this.lstFileDinhKem.forEach(file => {
        let tenFileDK = file.name.split('.')[0];
        if (tenFileDK == tenFileHD) {
          let endCodeDK = window.btoa(unescape(encodeURIComponent(file.data)));
          let fileDK = {
            order: 'BK',
            file_name: file.name,
            file_content: endCodeDK,
          }
          lstFile.push(fileDK);
        }
      });
    }
    this.frmData.input_invoice_files = lstFile;

    const valueDataSource = this.frmData.input_invoice_products.filter((item) => {
      if (item.product_name && item.product_group) {
        return item;
      }
    });

    if (this.formatter)
      this.frmData.decimal_places_json = JSON.stringify(VChanggeMoneyService.getObjFormatterToColumn(this.formatter));
    const dataForm = {
      input_invoice: this.frmData,
      input_invoice_products: valueDataSource,
      input_invoice_files: this.frmData.input_invoice_files,
    };
    for (const [key] of Object.entries(dataForm.input_invoice)) {
      if (key === 'id') {
        delete dataForm.input_invoice[key];
      }
      if (key === 'input_invoice_products') {
        delete dataForm.input_invoice[key];
      }
      if (key === 'input_invoice_files') {
        delete dataForm.input_invoice[key];
      }
    }
    dataForm.input_invoice_products.forEach(value => {
      for (const [key] of Object.entries(value)) {
        if (key === 'id') {
          delete value[key];
        }
      }
    });
    return dataForm;
  }

  // done
  addValueToFormFormat2(object, nameFile, dataBase64Code, signature) {
    const invoice_type_name = nameFile;
    let signature_content = signature;
    const template_code = object['inv:templateCode'];
    let adjustment_type;
    if (object['inv:adjustmentType']) {
      if (object['inv:adjustmentType'].trim() === '') {
        adjustment_type = '1';
      } else {
        adjustment_type = object['inv:adjustmentType'];
      }
    } else {
      adjustment_type = '1';
    }
    const invoice_number = object['inv:invoiceNumber'];
    const invoice_series = object['inv:invoiceSeries'];
    const currency_code = object['inv:currencyCode'];
    let invoice_date;
    let sign_date;
    if (object['inv:signedDate']) {
      const date = new Date(object['inv:signedDate']);
      if (date.toString() === 'Invalid Date') {
        sign_date = moment(object['inv:signedDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
      } else {
        sign_date = moment(object['inv:signedDate']).format('YYYY-MM-DD hh:mm:ss');
      }
    }
    if (object['inv:invoiceIssuedDate']) {
      const date = new Date(object['inv:invoiceIssuedDate']);
      if (date.toString() === 'Invalid Date') {
        invoice_date = moment(object['inv:invoiceIssuedDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
      } else {
        invoice_date = moment(object['inv:invoiceIssuedDate']).format('YYYY-MM-DD hh:mm:ss');
      }
    }
    const seller_tax_code = object['inv:sellerTaxCode'];
    const seller_fax_number = object['inv:sellerFaxNumber'];
    const seller_contact_name = object['inv:sellerContactPersonName'];
    const seller_address = object['inv:sellerAddressLine'];
    const seller_mobile = object['inv:sellerPhoneNumber'];
    const seller_legal_name = object['inv:sellerLegalName'];
    const buyer_legal_name = object['inv:buyerLegalName'];
    const buyer_tax_code = object['inv:buyerTaxCode'];
    const buyer_mobile = object['inv:buyerPhoneNumber'];
    const buyer_email = object['inv:buyerEmail'];
    const currency_rate = object['inv:exchangeRate'] ? object['inv:exchangeRate'] : object['inv:ExchangeRate'];
    let seller_bank_account;
    if (object.hasOwnProperty('inv:sellerBankAccount')) {
      if (object['inv:sellerBankAccount']) {
        seller_bank_account = object['inv:sellerBankAccount'] + ' - ' + object['inv:sellerBankName'];
      } else {
        seller_bank_account = object['inv:sellerBankName'];
      }
    } else {
      seller_bank_account = object['inv:sellerBankName'];
    }
    const payments = object['inv:payments']['inv:payment'][0];
    let payment_method_view_name;
    if (payments.hasOwnProperty('inv:paymentMethodName')) {
      payment_method_view_name = payments['inv:paymentMethodName'][0];
    } else {
      payment_method_view_name = payments['inv:paymentMethodNameExt'][0];
    }
    let seller_email;
    if (object['inv:sellerEmail'] === '.') {
      seller_email = null;
    } else {
      seller_email = object['inv:sellerEmail'];
    }
    let input_invoice_products;
    if (object['inv:items']) {
      const arrItem = object['inv:items']['inv:item'];
      for (let i = 0; i < arrItem.length; i++) {
        for (const keyItem in arrItem[i]) {
          if (arrItem[i].hasOwnProperty(keyItem)) {
            arrItem[i][keyItem] = arrItem[i][keyItem][0].trim();
          }
        }
      }
      const arrItemFormat = this.invoiceBuyService.formatArrayItem1(arrItem);
      input_invoice_products = arrItemFormat.filter((item) => {
        if (item.product_name !== 'Tng tin hng' && item.product_name !== 'Tng tin chit khu' && item.product_name !== 'Chit khu') {
          return item;
        }
      });
    }
    const amount = parseInt(object['inv:totalAmountWithoutVAT'].replace(',', '.'), 10);
    const total_amount_vat = parseInt(object['inv:totalVATAmount'].replace(',', '.'), 10);
    const total_payment = parseInt(object['inv:totalAmountWithVAT'].replace(',', '.'), 10);
    const total_payment_in_word = object['inv:totalAmountWithVATInWords'];
    let lstFile = [];
    lstFile.push({
      order: 'DK',
      file_name: nameFile,
      file_content: dataBase64Code,
      is_file_import: true
    });
    if (this.lstFileDinhKem && this.lstFileDinhKem.length > 0) {
      let tenFileHD = nameFile.split('.')[0];
      this.lstFileDinhKem.forEach(file => {
        let tenFileDK = file.name.split('.')[0];
        if (tenFileDK == tenFileHD) {
          let endCodeDK = window.btoa(unescape(encodeURIComponent(file.data)));
          let fileDK = {
            order: 'BK',
            file_name: file.name,
            file_content: endCodeDK,
          }
          lstFile.push(fileDK);
        }
      });
    }
    const input_invoice_files = lstFile;
    return {
      input_invoice: {
        total_payment_in_word: total_payment_in_word,
        total_payment: total_payment,
        total_amount_vat: total_amount_vat,
        amount: amount,
        seller_bank_account: seller_bank_account,
        buyer_email: buyer_email,
        buyer_mobile: buyer_mobile,
        buyer_tax_code: buyer_tax_code,
        buyer_legal_name: buyer_legal_name,
        seller_fax_number: seller_fax_number,
        seller_mobile: seller_mobile,
        invoice_series: invoice_series,
        seller_legal_name: seller_legal_name,
        invoice_number: invoice_number,
        currency_code: currency_code,
        currency_rate: currency_rate,
        adjustment_type: adjustment_type,
        seller_tax_code: seller_tax_code,
        seller_address: seller_address,
        payment_method_view_name: payment_method_view_name,
        template_code: template_code,
        invoice_type_name: invoice_type_name,
        invoice_date: invoice_date,
        seller_email: seller_email,
        seller_contact_name: seller_contact_name,
        origin_id: null,
        note: null,
        seller_surrogate_name: null,
        seller_tax_department: null,
        buyer_name: null,
        auto_value: false,
        auto_value_price: false,
        amount_vat5: null,
        amount_vat10: null,
        amount_vatx: null,
        amount_vat0: null,
        total_amount_product_vat5: null,
        total_amount_product_vat10: null,
        total_amount_product_vatx: null,
        total_amount_product_vat0: null,
        amount_discount: null,
        system_code: null,
        export_from_ihd: 0,
        signature_content: signature_content,
        sign_date: sign_date,
        version_xml: "1",
        xml_type: "2",
        is_import_from_zip: true,
        json_data: {
          statusCert: null,
          signDateCompareCert: null,
          signDateCompareInv: null,
          statusFileXml: null,
          capBoi: null,
          kyBoi: null,
          thoiHanChungThu: null,
          thoiGianKyHoaDon: null,
          tax_code: null,
          name: null,
          business_address: null,
          status: null,
          mstNB: null
        }
      },
      input_invoice_products: input_invoice_products,
      input_invoice_files: input_invoice_files
    };
  }

  setCollap() {
    this.isCollapsed = !this.isCollapsed;
  }

  exportExcel() {
    for (let i = 0; i < this.arrDataError.length; i++) {
      for (const [key] of Object.entries(this.arrDataError[i])) {
        if (key === 'invoice_number') {
          delete this.arrDataError[i][key];
        }
        if (key === 'template_code') {
          delete this.arrDataError[i][key];
        }
        if (key === 'invoice_series') {
          delete this.arrDataError[i][key];
        }
      }
    }
    Library.showloading();
    this.invoiceBuyService.apiExportFileExcel(this.arrDataError)
      .subscribe(rs => {
        if (rs.status === 'success') {
          const file = rs.data.report;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
          Library.notify('Xut file Excel thnh cng', 'success');
        } else {
          Library.notify(rs.message, 'error');
        }
      }, error => {
        Library.notify(error, 'error');
      },
        () => Library.hideloading()
      );
  }
}
