import { AfterViewInit, Component, Input, OnInit, ViewChild } from '@angular/core';
import { DxDataGridComponent } from 'devextreme-angular';
import { Subscription } from 'rxjs';
import { InvoiceModel, VatService } from '@modules/vat/shared';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { ActivatedRoute, Router } from '@angular/router';
import { ProductsService } from '@modules/products/shared';
import { AuthService } from '@core/services';
import { TranslateService } from '@ngx-translate/core';
import { Helper } from '@core/helper/Helper';
import { ConfigDecimalComponent } from '@shared/modules/vat-shared/components/config-decimal/config-decimal.component';
import { VChanggeMoneyService } from '@core/library/invoice/v-changge-money.service';

@Component({
  selector: 'app-product-grip',
  templateUrl: './product-grip.component.html',
  styleUrls: ['./product-grip.component.scss']
})
export class ProductGripComponent implements OnInit, AfterViewInit {

  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  routeSub: Subscription;
  dataSource1: any;
  @Input() set dataSource(value) {
    this.dataSource1 = value;
    this.createUpdateEvent();
  }
  @Input() invoice_products: any;
  @Input() formatter: any;
  @Input() isHdGTGT: any;
  @Input() _objTemplateSelect: any;
  @Input() extentionGrid: any;
  @Input() formDataInInvoice: any;
  select: any = {};
  _translate: any;
  frmData: InvoiceModel = new InvoiceModel(1);
  bsModalRef: BsModalRef;
  showPopup = false; // Popup
  ttDaXuatHoaDon = false;
  configs: any;
  amount_after_vat: any = 0;
  system_parameter: any = {};
  rowData = 1;

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private modalService: BsModalService,
    private vatService: VatService,
    private productsService: ProductsService,
    private authService: AuthService,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngAfterViewInit() {
  }

  ngOnInit() {
    this.select.dataHDMV = [
      { code: '1', name: 'HHDV dng ring' },
      { code: '2', name: 'HHDV dng chung' },
      { code: '3', name: 'HHDV dng cho d n u t' },
      { code: '4', name: 'HHDV khng  iu kin' },
    ];
    this.select.vats = [
      { name: 'Khng chu thu', code: -1 },
      { name: 'Khng k khai tnh thu', code: -2 },
      { name: '0%', code: 0 },
      { name: '5%', code: 5 },
      { name: '8%', code: 8 },
      { name: '10%', code: 10 },
    ];
    this.configs = this.authService.getConfigs();
  }

  createUpdateEvent() {
    setTimeout(() => {
      this.invoice_products.on('updated', (key, value) => {
        const column = Object.keys(value)[0];
        if (value.product_code !== 'add') {
          this.invoice_products.byKey(key).then((row) => {
            switch (column) {
              case 'quantity':
              case 'price':
              case 'vat':
              case 'amount':
              case 'amount_vat':
                this._tinhGiaTri(key, row, value);
                break;
              default:
                break;
              // }
            }
          }, (error) => {
            alert(error);
          });
        } else {
          this.invoice_products.remove(key);
          this.addRow();
        }
      });
    }, 200);
  }

  _tinhGiaTri(key, row, value) {
    if (row.product_name) {
      if (row.quantity && row.price) {
        row.amount = row.quantity * row.price;
      }
      if (row.amount) {
        if (row.vat && row.vat !== 0 && row.vat !== -1 && row.vat !== -2) {
          row.amount_vat = (row.amount * row.vat) / 100;
        } else if (row.vat === 0 || row.vat === -1 || row.vat === -2) {
          row.amount_vat = 0;
        }
        row.amount_after_vat = row.amount + row.amount_vat;
      }
      this.formDataInInvoice.total_payment = 0;
      this.formDataInInvoice.amount = 0;
      this.formDataInInvoice.total_amount_vat = 0;
      this.invoice_products.load().done(item => {
        item.forEach(
          valueItem => {
            this.formDataInInvoice.total_payment += valueItem.amount_after_vat;
            this.formDataInInvoice.amount += valueItem.amount;
            this.formDataInInvoice.total_amount_vat += valueItem.amount_vat;
          }
        );
      });
      let currency_code = this.formDataInInvoice.currency_code;
      if (this.configs.system_parameter.view_price_nte) {
        currency_code = 'VND';
      }
      let number_money = Math.abs(this.formDataInInvoice.total_payment);
      let c_money = Helper.tienBangChu(number_money, VChanggeMoneyService.getFormatterToNumber(this.formatter.tong_tien), currency_code, this.configs.system_parameter.display_read_money_english);
      this.formDataInInvoice.total_payment_in_word = c_money;
      if (this.formDataInInvoice.adjustment_type == "5") {
        if (this.formDataInInvoice.total_payment < 0) {
          this.formDataInInvoice.total_payment_in_word = "Gim " + c_money;
        }
        else if (this.formDataInInvoice.total_payment > 0) {
          this.formDataInInvoice.total_payment_in_word = "Tng " + c_money;
        }
      }
      return row;
    }
  }

  onGripKeyDown(e) {
    if (e.event.code === 'Delete') {
      e.event.srcElement.value = null;
    }
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      e.rowElement.style.whiteSpace = 'pre-wrap';
      const eles = e.rowElement.getElementsByTagName('td');
      if (e.data.error) {
        const ele = eles[eles.length - 1];
        ele.style.background = 'red url(\'assets/img/icons/fa-remove.png\') no-repeat 2% center';
        ele.title = e.data.error;
      }
    }
  }

  onEditorPreparing(e) {
    if (e.parentType === 'dataRow') {
      if (e.dataField === 'product_code') {
        e.editorOptions.onOpened = function (arg) {
          const popupInstance = arg.component._popup;
          popupInstance.option('width', 400);
          popupInstance.option('minWidth', 400);
        };
      }
      if (e.dataField === 'product_name') {
        e.editorName = 'dxTextArea';
      }

    }
  }

  onEditorPrepared(e) {
    if ((e.dataField === 'amount_without_discount' || e.dataField === 'discount' || e.dataField === 'amount_discount' || e.dataField === 'quantity' || e.dataField === 'price' || e.dataField === 'amount' || e.dataField === 'amount_vat' || e.dataField === 'amount_after_vat') && e.parentType === 'dataRow') {
      e.editorElement.childNodes[1].firstChild.onwheel = (evt) => {
        e.component.cancelEditData();
      };
      e.editorElement.childNodes[1].firstChild.addEventListener('select', function () {
        this.selectionStart = this.selectionEnd;
      }, false);
    }
  }

  addRow() {
    let len, order, orderLast;
    len = this.dataSource1.items().length;
    orderLast = this.dataSource1.items()[len - 1].order;
    for (let i = 0; i < this.rowData; i++) {
      if (this.dataSource1.items().length >= 1) {
        order = ++orderLast;
      } else {
        order = 1;
      }
      const data = this.add_invoice_products_data(order);
      this.invoice_products.insert(data);
    }
    this.dataSource1.reload();
  }

  add_invoice_products_data(order) {
    return {
      id: Helper.makeid(),
      order: order,
      product_code: '',
      product_name: '',
      unit_code: null,
      unit_name: '',
      quantity: null,
      price: null,
      vat: null,
      amount_vat: null,
      amount: null,
      product_group: null,
      amount_after_vat: null
    };
  }

  roundNumber(number, n): number {
    const p = Math.pow(10, n);
    return Math.round(number * p) / p;
  }

  configDecimal(n) {
    const initialState = {
      column: n,
      formatter: this.formatter,
      showDonGiaNte: this.system_parameter.view_price_nte,
    };
    this.bsModalRef = this.modalService.show(ConfigDecimalComponent, {
      class: 'modal-lg', initialState, backdrop: 'static',
      keyboard: false
    });
    const subModal = this.modalService.onHide.subscribe(() => {
      const data = this.bsModalRef.content.successSaveItem;
      if (data) {
        // this.formatter[data.column] = data.character;
      }
      subModal.unsubscribe();
    });
  }

  // Xa hng
  deleteRow(item) {
    if (confirm(this._translate.CONFIRM.delete)) {
      this.invoice_products.remove(item.id);
      this.formDataInInvoice.total_payment = 0;
      this.formDataInInvoice.total_amount_vat = 0;
      this.formDataInInvoice.amount = 0;
      this.invoice_products.load().done(data => {
        let _index = 0;
        data.forEach(
          value => {
            value.order = ++_index;
            if (value.amount) {
              this.formDataInInvoice.amount += value.amount;
            }
            if (value.amount_after_vat) {
              this.formDataInInvoice.total_payment += value.amount_after_vat;
            }
            if (value.amount_vat) {
              this.formDataInInvoice.total_amount_vat += value.amount_vat;
            }
          }
        );
      });
      this.dataSource1.reload();
    }
  }

  // Np d liu bng k
  napDuLieuTuBangKe() {
    document.getElementById('file-bang-ke').click();
  }

  taxCodeChoose(e) {
  }
}
