import { Component, OnInit } from '@angular/core';
import { AuthService } from '@core/services';
import { BsModalRef } from 'ngx-bootstrap';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-check-invoice-number',
  templateUrl: './check-invoice-number.component.html',
  styleUrls: ['./check-invoice-number.component.scss']
})
export class CheckInvoiceNumberComponent implements OnInit {

  isError : any;
  message : any;

  _translate: any;
  _language: string;
  data: any = {};

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    public translate: TranslateService
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
  }

  ngOnInit() {
  }

  close() {
    this.bsModalRef.hide();
  }

  demo() {
    const win = window.open('http://tracuuhoadon.gdt.gov.vn/tbphtc.html', '_blank');
    win.focus();
  }
}
