import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { AuthService } from '@core/services';
import { ActivatedRoute } from '@angular/router';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { TranslateService } from '@ngx-translate/core';
import { NotifyTaxCodeComponent } from '@modules/invoice-buy/shared/modal/notify-tax-code/notify-tax-code.component';
import { CheckInvoiceNumberComponent } from '@modules/invoice-buy/shared/modal/check-invoice-number/check-invoice-number.component';

import xml2js from 'xml2js';
import * as moment from 'moment';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { Library } from '@core/library';
import { Invoice } from '@modules/invoice-buy/shared/class/invoice.model';
import DataSource from 'devextreme/data/data_source';
import { VatService } from '@modules/vat/shared';
import { VStringService } from '@core/library/public/v-string.service';
import { CustomersService } from '@modules/customers/shared';
import { DxFormComponent } from 'devextreme-angular';
import { sEnum, Helper, VChanggeMoneyService } from '@core/index';

@Component({
  selector: 'app-form-header-invoice',
  templateUrl: './form-header-invoice.component.html',
  styleUrls: ['./form-header-invoice.component.scss']
})
export class FormHeaderInvoiceComponent implements OnInit {
  showPopup = false;
  _translate: any;
  @ViewChild(DxFormComponent) form: DxFormComponent;
  @ViewChild('myInput') myFile: ElementRef;
  @Input() dataSource: any;
  @Input() invoice_products: any;
  @Input() frmData: any;
  @Input() formatter: any;
  @Input() _objTemplateSelect: any;
  @Output('onFormChange') formEvent = new EventEmitter<any>();
  @Input() extentionGrid: any;
  select: any = {};
  mst_btn_options = {
    text: '',
    icon: 'fa fa-search',
    type: 'default',
    onClick: () => {
      Library.showloading();
      this.customersService.getInfoCompany(this.frmData.seller_tax_code).subscribe(
        (res: any) => {
          if (res.status === 'success') {
            if (res.data.customer.active_status === 1) {
              const initialState = {
                data: res.data.customer,
                isShow: false,
              };
              this.bsModalRef = this.modalService.show(NotifyTaxCodeComponent, {
                class: 'modal-md', initialState, backdrop: 'static',
                keyboard: false
              });
            } else {
              const initialState = {
                isShow: true,
                message: this._translate.NOTIFY.system_not_found
              };
              this.bsModalRef = this.modalService.show(NotifyTaxCodeComponent, {
                class: 'modal-md', initialState, backdrop: 'static',
                keyboard: false
              });
            }
          } else {
            // Library.notify(res.message, 'error');
            const initialState = {
              isShow: true,
              message: this._translate.NOTIFY.system_not_found
            };
            this.bsModalRef = this.modalService.show(NotifyTaxCodeComponent, {
              class: 'modal-md', initialState, backdrop: 'static',
              keyboard: false
            });
          }
        }, error => Library.notify(error, 'error'),
        () => Library.hideloading()
      );
    }
  };
  emails: any = [];
  customer_codes: any = [];
  fileReader = new FileReader();
  htXuatOptions = [];

  tax_code_local: string = null;
  tax_codes: any = [];
  readOnlyCacTruongConLai: boolean;

  constructor(
    private authService: AuthService,
    private route: ActivatedRoute,
    private modalService: BsModalService,
    public bsModalRef: BsModalRef,
    public translate: TranslateService,
    private invoiceBuyService: InvoiceBuyService,
    private vatService: VatService,
    private customersService: CustomersService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    const dataUser = JSON.parse(localStorage.getItem('currentUser'));
    this.tax_code_local = dataUser.account.tax_code;
    this.route.data
      .map(value => value.FormResolverService)
      .subscribe((data: any) => {
        this.setDataConfig(data[0]);
        if (this.frmData.seller_email) {
          this.emails = [];
          const stringDataEmailGet = this.frmData.seller_email.split(';');
          for (let i = 0; i < stringDataEmailGet.length; i++) {
            this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
          }
        }
      });
    this.tax_codes = new DataSource({
      load: (loadOptions) => {
        return this.vatService.getDataSourceForm(loadOptions.searchValue, 'tax_code');
      }
    });
  }

  taxCodeChoose(e) {
    const customer = e.itemData;
    this.frmData.seller_legal_name = customer.name;
    this.frmData.seller_address = customer.business_address;
    this.frmData.seller_mobile = customer.contact_mobile;
    if (customer.email) {
      this.frmData.seller_email = customer.email;
      this.emails = [];
      const stringDataEmailGet = customer.email.split(';');
      for (let i = 0; i < stringDataEmailGet.length; i++) {
        this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
      }
    } else {
      this.frmData.seller_email = customer.email;
      this.emails = [];
    }
    if (customer.bank_account_number) {
      this.frmData.seller_bank_account = customer.bank_account_number + ' - ' + customer.bank_name;
    } else {
      this.frmData.seller_bank_account = customer.bank_name;
    }
    this.onFieldDataChanged();
  }

  setDataConfig(data) {
    this.select.payment_method_names = data.DM_HINH_THUC_THANH_TOAN;
    this.select.currency_codes = data.DM_LOAI_TIEN;
    this.select.invoice_type = [
      { code: '1', name: 'Ha n gc' },
      { code: '2', name: this._translate.GRID.GENERAL.invoice_replaced },
      { code: '3', name: 'Ha n thay th' },
      { code: '4', name: this._translate.GRID.GENERAL.invoice_adjusted },
      { code: '5', name: this._translate.GRID.GENERAL.adjustment_invoice },
      { code: '6', name: this._translate.GRID.GENERAL.invoice_deleted },
      { code: '7', name: this._translate.GRID.GENERAL.invoice_removed },
    ];
  }

  renderDataEmail($event) {
    const arrEmail = [];
    this.emails = $event;
    if (this.emails.length > 0) {
      for (let i = 0; i < this.emails.length; i++) {
        if (typeof this.emails[i] === 'object')
          arrEmail.push(this.emails[i].value);
        else
          if (typeof this.emails[i] === 'string')
            arrEmail.push(this.emails[i]);
      }
      const emailToString = arrEmail.toString();
    } else {
    }
  }

  fileChanged($event) {
    this.frmData = new Invoice('1');
    this.emails = [];
    this.onFieldDataChanged();
    this.frmData.version_xml = "1";
    const fileContent = $event.target.files[0];
    let nameFile;
    if (fileContent.name) {
      nameFile = fileContent.name;
    }
    const fileReader = new FileReader();
    fileReader.onloadend = (x) => {
      this.parseXMLtoObject(fileReader.result, nameFile);
    };
    fileReader.readAsText(fileContent);
  }

  parseXMLtoObject(data, nameFile) {
    const dataBase64Code = window.btoa(unescape(encodeURIComponent(data)));
    // paser to base64 to a string
    // const b = decodeURIComponent(escape(window.atob(a)));
    xml2js.parseString(data, (err, result) => {
      if (result) {
        if (result.hasOwnProperty('Invoice')) {
          let X509Certificate = '';
          if (result['Invoice']['Signature'])
            X509Certificate = result['Invoice']['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0]
          this.getValueToObject('dreamSoft', result['Invoice']['Content'][0], nameFile, dataBase64Code, X509Certificate);
        } else if (result.hasOwnProperty('inv:invoice')) {
          let X509Certificate = '';
          if (result['inv:invoice']['Signature'])
            X509Certificate = result['inv:invoice']['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0];
          this.getValueToObject('all', result['inv:invoice']['inv:invoiceData'][0], nameFile, dataBase64Code, X509Certificate);
        } else if (result.hasOwnProperty('inv:transaction')) {
          let X509Certificate = '';
          if (result['inv:transaction']['inv:invoices'][0]['inv:invoice'][0]['Signature'])
            X509Certificate = result['inv:transaction']['inv:invoices'][0]['inv:invoice'][0]['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0];
          this.getValueToObject('all', result['inv:transaction']['inv:invoices'][0]['inv:invoice'][0]['inv:invoiceData'][0], nameFile, dataBase64Code, X509Certificate);
        } else if (result.hasOwnProperty("HDon")) {
          this.readDataFromXmlTCT(result, nameFile, dataBase64Code);
        }
        else {
          Library.notify('File d liu khng phi file ha n!', 'error');
        }
      } else {
        Library.notify('File d liu khng ng!', 'error');
      }
    }
    );
  }

  getValueToObject(type, value, nameFile, dataBase64Code, signature) {
    const object = {};
    for (const property in value) {
      if (value.hasOwnProperty(property)) {
        if (typeof value[property][0] === 'string') {
          object[property] = value[property][0].trim();
        } else {
          object[property] = value[property][0];
        }
      }
    }
    if (type === 'dreamSoft') {
      if (object['CusTaxCode']) {
        if (object['CusTaxCode'] === this.tax_code_local) {
          this.addValueToFormFormat1(object, nameFile, dataBase64Code, signature);
        } else {
          Library.notify(this._translate.NOTIFY.tax_code_diffirent_error, 'error');
        }
      } else {
        Library.notify('Cha h tr c nh dng file ha n ny', 'error');
      }
    } else if (type === 'all') {
      if (object['inv:buyerTaxCode']) {
        if (object['inv:buyerTaxCode'] === this.tax_code_local) {
          this.addValueToFormFormat2(object, nameFile, dataBase64Code, signature);
        } else {
          Library.notify(this._translate.NOTIFY.tax_code_diffirent_error, 'error');
        }
      } else {
        Library.notify('Cha h tr c nh dng file ha n ny', 'error');
      }
    }
  }

  addValueToFormFormat1(object, nameFile, dataBase64Code, signature) {
    this.frmData.invoice_type_name = nameFile;
    this.frmData.signature_content = signature;
    if (object['ArisingDate'])
      this.frmData.invoice_date = moment(object['ArisingDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
    if (object['SignDate'])
      this.frmData.sign_date = moment(object['SignDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
    this.frmData.template_code = object['InvoicePattern'];
    if (object.hasOwnProperty('Kind_of_Payment')) {
      this.frmData.payment_method_view_name = object['Kind_of_Payment'];
    } else {
      this.frmData.payment_method_view_name = object['PaymentMethod'];
    }
    this.frmData.invoice_series = object['SerialNo'];
    this.frmData.seller_legal_name = object['ComName'];
    this.frmData.invoice_number = object['InvoiceNo'];
    this.frmData.currency_code = object['CurrencyUnit'];
    if (object['ExchangeRate']) {
      this.frmData.currency_rate = object['ExchangeRate'].replace(',', '.');
    }
    this.frmData.adjustment_type = '1';
    this.frmData.seller_tax_code = object['ComTaxCode'];
    this.frmData.seller_address = object['ComAddress'];
    this.frmData.seller_mobile = object['ComPhone'];
    if (object['ComEmail']) {
      if (object['ComEmail'] !== '.') {
        this.frmData.seller_email = object['ComEmail'];
        this.emails = [];
        const stringDataEmailGet = object['ComEmail'].split(';');
        for (let i = 0; i < stringDataEmailGet.length; i++) {
          this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
        }
      } else {
        this.frmData.seller_email = null;
        this.emails = [];
      }
    } else {
      this.frmData.seller_email = null;
      this.emails = [];
    }

    if (object.hasOwnProperty('ComFaxCode')) {
      this.frmData.seller_fax_number = object['ComFaxCode'];
    }
    this.frmData.buyer_legal_name = object['CusName'];
    this.frmData.buyer_tax_code = object['CusTaxCode'];
    this.frmData.buyer_mobile = object['CusPhone'];
    this.frmData.buyer_address = object['CusPhone'];
    this.frmData.buyer_email = object['CusEmail'];
    this.frmData.xml_type = "2";
    if (object.hasOwnProperty('ComBankNo')) {
      if (object['ComBankName']) {
        this.frmData.seller_bank_account = object['ComBankNo'] + ' - ' + object['ComBankName'];
      } else {
        this.frmData.seller_bank_account = object['ComBankName'];
      }
    }
    this.frmData.amount = parseInt(object['Total'].replace(',', '.'), 10);
    if (object.hasOwnProperty('VATAmount')) {
      this.frmData.total_amount_vat = parseInt(object['VATAmount'].replace(',', '.'), 10);
    } else {
      this.frmData.total_amount_vat = parseInt(object['VAT_Amount'].replace(',', '.'), 10);
    }
    this.frmData.total_payment = parseInt(object['Amount'].replace(',', '.'), 10);
    if (object.hasOwnProperty('Amount_words')) {
      this.frmData.total_payment_in_word = object['Amount_words'];
    } else {
      this.frmData.total_payment_in_word = object['AmountInWords'];
    }
    this.frmData.input_invoice_files = [{
      order: 'DK',
      file_name: nameFile,
      file_content: dataBase64Code,
      is_file_import: true
    }];
    if (object['Products']) {
      const arrItem = object['Products']['Product'];
      for (let i = 0; i < arrItem.length; i++) {
        for (const keyItem in arrItem[i]) {
          if (arrItem[i].hasOwnProperty(keyItem)) {
            arrItem[i][keyItem] = arrItem[i][keyItem][0].trim();
          }
        }
      }
      const arrItemFormat = this.invoiceBuyService.formatArrayItem(arrItem);
      arrItemFormat.forEach((value, index) => {
        if (value.quantity) {
          value.quantity = parseInt(value.quantity.replace(',', '.'), 10);
        } else {
          value.quantity = null;
        }
        if (!value.product_code) {
          value.product_code = null;
        }
        if (value.price) {
          value.price = parseInt(value.price.replace(',', '.'), 10);
        } else {
          value.price = null;
        }
        if (value.amount) {
          value.amount = parseInt(value.amount.replace(',', '.'), 10);
        } else {
          value.amount = null;
        }
        if (value.vat) {
          value.vat = parseInt(value.vat.replace(',', '.'), 10);
        } else {
          value.vat = null;
        }
        if (!value.unit_name) {
          value.unit_name = null;
        }
        if (value.amount_vat) {
          value.amount_vat = parseInt(value.amount_vat.replace(',', '.'), 10);
        } else {
          value.amount_vat = null;
        }
        if (value.unit_code === undefined) {
          value.unit_code = null;
        }
        if (value.amount && value.amount_vat) {
          value.amount_after_vat = value.amount + value.amount_vat;
        } else if (value.amount && !value.amount_vat) {
          value.amount_after_vat = value.amount;
        } else {
          value.amount_after_vat = null;
        }
        value.order = index + 1;
      });
      this.frmData.input_invoice_products = arrItemFormat;
    }
    this.onFieldDataChanged();
  }

  /* c file xml t Tng Cc Thu */
  readDataFromXmlTCT(content, nameFile, dataBase64Code) {
    let TTChung = content['HDon']['DLHDon'][0]['TTChung'][0];
    let NBan = content['HDon']['DLHDon'][0]['NDHDon'][0]['NBan'][0];
    let NMua = content['HDon']['DLHDon'][0]['NDHDon'][0]['NMua'][0];
    let DSHHDVu = content['HDon']['DLHDon'][0]['NDHDon'][0]['DSHHDVu'][0]['HHDVu'];
    let TToan = content['HDon']['DLHDon'][0]['NDHDon'][0]['TToan'][0];
    let X509Certificate = content['HDon']['DSCKS'][0]['NBan'][0]['Signature'][0]['KeyInfo'][0]['X509Data'][0]['X509Certificate'][0];
    let THTTLTSuat = [];
    if (content['HDon']['DLHDon'][0]['NDHDon'][0]['TToan'][0]['THTTLTSuat'])
      THTTLTSuat = content['HDon']['DLHDon'][0]['NDHDon'][0]['TToan'][0]['THTTLTSuat'][0]['LTSuat'];
    let MST_BENMUA = content['HDon']['DLHDon'][0]['NDHDon'][0]['NMua'][0]["MST"];
    let signDate = content['HDon']['DSCKS'][0]['NBan'][0]['Signature'][0]['Object'][0]['SignatureProperties'][0]['SignatureProperty'][0]['SigningTime'][0]._;
    let TTHDLQuan = content['HDon']['DLHDon'][0]['TTChung'][0]["TTHDLQuan"];
    if (TTHDLQuan) {
      let TCHDon = content['HDon']['DLHDon'][0]['TTChung'][0]["TTHDLQuan"][0]["TCHDon"];
      if (TCHDon) {
        TCHDon = content['HDon']['DLHDon'][0]['TTChung'][0]["TTHDLQuan"][0]["TCHDon"][0];
        if (TCHDon == "1")
          this.frmData.adjustment_type = "3";
        else if (TCHDon == "2")
          this.frmData.adjustment_type = "5";
      }
      else
        this.frmData.adjustment_type = "1";
    }
    else
      this.frmData.adjustment_type = "1";
    if (!signDate)
      signDate = content['HDon']['DSCKS'][0]['NBan'][0]['Signature'][0]['Object'][0]['SignatureProperties'][0]['SignatureProperty'][0]['SigningTime'][0];
    let MaCQT = "";
    if (content['HDon']['MCCQT'])
      MaCQT = content['HDon']['MCCQT'][0]._;
    if (MST_BENMUA) {
      MST_BENMUA = content['HDon']['DLHDon'][0]['NDHDon'][0]['NMua'][0]["MST"][0];
      MST_BENMUA = MST_BENMUA.trim();
      if (MST_BENMUA === this.tax_code_local) {
      } else {
        Library.notify("M s thu ngi mua trn ha n khng trng vi M s thu ca n v", 'error');
        return;
      }
    } else {
      Library.notify('M s thu ngi mua trn ha n khng trng vi M s thu ca n v', 'error');
      return;
    }
    this.frmData.invoice_type_name = nameFile;
    this.frmData.tax_authority_code = MaCQT;
    this.frmData.xml_type = "3";
    /* Thng tin chung */
    for (const property in TTChung) {
      let valueI = TTChung[property][0];
      switch (property) {
        case "KHMSHDon":
          this.frmData.template_code = valueI;
          break;
        case "KHHDon":
          this.frmData.invoice_series = valueI;
          break;
        case "SHDon":
          this.frmData.invoice_number = valueI;
          break;
        case "NLap":
          const date = new Date(valueI);
          if (date.toString() === 'Invalid Date') {
            this.frmData.invoice_date = moment(valueI, 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
          } else {
            this.frmData.invoice_date = moment(valueI).format('YYYY-MM-DD hh:mm:ss');
          }
          break;
        case "DVTTe":
          this.frmData.currency_code = valueI;
          break;
        case "HTTToan":
          this.frmData.payment_method_view_name = valueI;
          break;
      }
    }
    /* Thng tin bn bn */
    for (const property in NBan) {
      let valueI = NBan[property][0];
      switch (property) {
        case "Ten":
          this.frmData.seller_legal_name = valueI;
          break;
        case "MST":
          this.frmData.seller_tax_code = valueI;
          break;
        case "DChi":
          this.frmData.seller_address = valueI;
          break;
        case "SDThoai":
          this.frmData.seller_mobile = valueI;
          break;
      }
    }
    /* Thng tin bn mua */
    for (const property in NMua) {
      let valueI = NMua[property][0];
      switch (property) {
        case "Ten":
          this.frmData.buyer_legal_name = valueI;
          break;
        case "MST":
          this.frmData.buyer_tax_code = valueI;
          break;
        case "DChi":
          this.frmData.buyer_address = valueI;
          break;
        case "SDThoai":
          this.frmData.buyer_mobile = valueI;
          break;
        case "MKHang":
          this.frmData.customer_code = valueI;
          break;
        case "DCTDTu":
          this.frmData.buyer_email = valueI;
          break;
        case "HVTNMHang":
          this.frmData.buyer_name = valueI;
          break;
      }
    }
    /* Thng tin S tin trn ha n */
    for (const property in TToan) {
      let valueI = TToan[property][0];
      switch (property) {
        case "TgTCThue":
          this.frmData.amount = valueI;
          break;
        case "TgTThue":
          this.frmData.total_amount_vat = valueI;
          break;
        case "TgTTTBSo":
          this.frmData.total_payment = valueI;
          break;
        case "TgTTTBChu":
          this.frmData.total_payment_in_word = valueI;
          break;
      }
    }
    if (THTTLTSuat && THTTLTSuat.length > 0) {
      let lstTThue = [];
      for (let i = 0; i < THTTLTSuat.length; i++) {
        const object = {};
        for (const property in THTTLTSuat[i]) {
          let valueI = THTTLTSuat[i][property][0];
          object[property] = valueI;
        }
        lstTThue.push(object);
      }
      if (lstTThue.length > 0) {
        lstTThue.forEach(itemT => {
          switch (itemT.TSuat) {
            case "0%":
              this.frmData.total_amount_product_vat0 = itemT.ThTien;
              break;
            case "5%":
              this.frmData.total_amount_product_vat5 = itemT.ThTien;
              this.frmData.amount_vat5 = itemT.TThue;
              break;
            case "8%":
              this.frmData.total_amount_product_vat8 = itemT.ThTien;
              this.frmData.amount_vat8 = itemT.TThue;
              break;
            case "10%":
              this.frmData.total_amount_product_vat10 = itemT.ThTien;
              this.frmData.amount_vat10 = itemT.TThue;
              break;
            case "KCT":
              this.frmData.total_amount_product_vatx = itemT.ThTien;
              break;
            case "KKKNT":
              this.frmData.total_amount_product_vat_kkknt = itemT.ThTien;
              break;
          }
        });
      }
    }
    /* Danh sch hng ha, dch v */
    if (DSHHDVu && DSHHDVu.length > 0) {
      let lstHangHoa = [];
      for (let i = 0; i < DSHHDVu.length; i++) {
        const object = {};
        for (const property in DSHHDVu[i]) {
          let valueI = DSHHDVu[i][property][0];
          object[property] = valueI;
        }
        lstHangHoa.push(object);
      }
      if (lstHangHoa.length > 0) {
        this.frmData.input_invoice_products = [];
        lstHangHoa.forEach(itemHH => {
          let oHangHoa = {};
          oHangHoa["order"] = itemHH.STT ? itemHH.STT : null;
          oHangHoa["product_code"] = itemHH.MHHDVu ? itemHH.MHHDVu : null;
          oHangHoa["product_name"] = itemHH.THHDVu ? itemHH.THHDVu : null;
          oHangHoa["unit_name"] = itemHH.DVTinh ? itemHH.DVTinh : null;
          oHangHoa["quantity"] = itemHH.SLuong;
          oHangHoa["price"] = itemHH.DGia;
          oHangHoa["amount"] = itemHH.ThTien;
          oHangHoa["product_group"] = "1";
          oHangHoa["unit_code"] = null;
          oHangHoa["is_promotion"] = null;
          // oHangHoa["view_order"] = oHangHoa["order"];
          switch (itemHH.TSuat) {
            case "0%":
              oHangHoa["vat"] = 0;
              break;
            case "5%":
              oHangHoa["vat"] = 5;
              break;
            case "10%":
              oHangHoa["vat"] = 10;
              break;
            case "8%":
              oHangHoa["vat"] = 8;
              break;
            case "KCT":
              oHangHoa["vat"] = -1;
              break;
            case "KKKNT":
              oHangHoa["vat"] = -2;
              break;
            default:
              oHangHoa["vat"] = null;
              break;
          }
          /* Cu hnh thp phn */
          //->S lng
          if (oHangHoa["quantity"]) {
            let arr = oHangHoa["quantity"].split('.');
            if (arr.length == 2) {
              this.formatter.so_luong = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.so_luong = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> n gi
          if (oHangHoa["price"]) {
            let arr = oHangHoa["price"].split('.');
            if (arr.length == 2) {
              this.formatter.don_gia = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.don_gia = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> Thnh tin
          if (oHangHoa["amount"]) {
            let arr = oHangHoa["amount"].split('.');
            if (arr.length == 2) {
              this.formatter.thanh_tien = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.thanh_tien = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> Tin thu
          if (this.frmData.total_amount_vat) {
            let arr = this.frmData.total_amount_vat.split('.');
            if (arr.length == 2) {
              this.formatter.tien_thue = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.tien_thue = VChanggeMoneyService.formatDecimalColumn(0);
          }
          //-> Tng tin
          if (this.frmData.total_payment) {
            let arr = this.frmData.total_payment.split('.');
            if (arr.length == 2) {
              this.formatter.tong_tien = VChanggeMoneyService.formatDecimalColumn(arr[1].length);
            } else
              this.formatter.tong_tien = VChanggeMoneyService.formatDecimalColumn(0);
          }

          if (oHangHoa["quantity"])
            oHangHoa["quantity"] = VChanggeMoneyService.roundNumber(Helper.toNumber(oHangHoa["quantity"]), this.formatter.so_luong);
          else
            oHangHoa["quantity"] = null;

          if (oHangHoa["price"])
            oHangHoa["price"] = VChanggeMoneyService.roundNumber(Helper.toNumber(oHangHoa["price"]), this.formatter.don_gia);
          else
            oHangHoa["price"] = null;

          if (oHangHoa["amount"])
            oHangHoa["amount"] = VChanggeMoneyService.roundNumber(Helper.toNumber(oHangHoa["amount"]), this.formatter.thanh_tien);
          else
            oHangHoa["amount"] = null;

          /* Tnh tin thu */
          if (oHangHoa["amount"] != null && oHangHoa["vat"] != null) {
            if (oHangHoa["vat"] > 0) {
              oHangHoa["amount_vat"] = VChanggeMoneyService.roundNumber((oHangHoa["amount"] * oHangHoa["vat"]) / 100, this.formatter.tien_thue);
            } else if (oHangHoa["vat"] == 0)
              oHangHoa["amount_vat"] = VChanggeMoneyService.roundNumber(0, this.formatter.tien_thue);
            else
              oHangHoa["amount_vat"] = null;
          } else if (oHangHoa["vat"] == null || (oHangHoa["vat"] != null && oHangHoa["amount"] == null))
            oHangHoa["amount_vat"] = null;
          /* Tnh tng tin hng ha */
          if (oHangHoa["amount_vat"] != null && oHangHoa["amount"] != null)
            oHangHoa["amount_after_vat"] = VChanggeMoneyService.roundNumber(oHangHoa["amount"] + oHangHoa["amount_vat"], this.formatter.tong_tien);
          else if (oHangHoa["amount"] != null)
            oHangHoa["amount_after_vat"] = VChanggeMoneyService.roundNumber(oHangHoa["amount"], this.formatter.tong_tien);
          else
            oHangHoa["amount_after_vat"] = null;

          this.frmData.input_invoice_products.push(oHangHoa);
        });
      }
    }

    /* Cu hnh thp phn cho s tin */
    if (this.frmData.total_amount_product_vat0)
      this.frmData.total_amount_product_vat0 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat0), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat0 = null;

    if (this.frmData.total_amount_product_vat5)
      this.frmData.total_amount_product_vat5 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat5), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat5 = null;

    if (this.frmData.total_amount_product_vat8)
      this.frmData.total_amount_product_vat8 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat8), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat8 = null;

    if (this.frmData.total_amount_product_vat10)
      this.frmData.total_amount_product_vat10 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat10), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat10 = null;

    if (this.frmData.total_amount_product_vatx)
      this.frmData.total_amount_product_vatx = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vatx), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vatx = null;

    if (this.frmData.total_amount_product_vat_kkknt)
      this.frmData.total_amount_product_vat_kkknt = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_product_vat_kkknt), this.formatter.thanh_tien);
    else
      this.frmData.total_amount_product_vat_kkknt = null;

    if (this.frmData.amount)
      this.frmData.amount = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount), this.formatter.thanh_tien);
    else
      this.frmData.amount = null;

    if (this.frmData.total_amount_vat)
      this.frmData.total_amount_vat = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_amount_vat), this.formatter.tien_thue);
    else
      this.frmData.total_amount_vat = null;

    if (this.frmData.total_payment)
      this.frmData.total_payment = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.total_payment), this.formatter.tong_tien);
    else
      this.frmData.total_payment = null;

    if (this.frmData.amount_vat5)
      this.frmData.amount_vat5 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount_vat5), this.formatter.tien_thue);
    else
      this.frmData.amount_vat5 = null;

    if (this.frmData.amount_vat8)
      this.frmData.amount_vat8 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount_vat8), this.formatter.tien_thue);
    else
      this.frmData.amount_vat8 = null;

    if (this.frmData.amount_vat10)
      this.frmData.amount_vat10 = VChanggeMoneyService.roundNumber(Helper.toNumber(this.frmData.amount_vat10), this.formatter.tien_thue);
    else
      this.frmData.amount_vat10 = null;

    this.frmData.input_invoice_products = this.frmData.input_invoice_products.filter((item) => {
      if (item.product_name !== 'Tng tin hng' && item.product_name !== 'Tng tin chit khu' && item.product_name !== 'Chit khu') {
        return item;
      }
    });
    this.frmData.signature_content = X509Certificate;
    if (signDate)
      this.frmData.sign_date = signDate.replace("T", " ");
    this.frmData.input_invoice_files = [{
      order: 'DK',
      file_name: nameFile,
      file_content: dataBase64Code,
      is_file_import: true
    }];
    this.onFieldDataChanged();
  }

  addValueToFormFormat2(object, nameFile, dataBase64Code, signature) {
    this.frmData.invoice_type_name = nameFile;
    this.frmData.signature_content = signature;
    this.frmData.template_code = object['inv:templateCode'];
    if (object['inv:adjustmentType']) {
      if (object['inv:adjustmentType'].trim() === '') {
        this.frmData.adjustment_type = '1';
      } else {
        this.frmData.adjustment_type = object['inv:adjustmentType'];
      }
    } else {
      this.frmData.adjustment_type = '1';
    }
    this.frmData.invoice_number = object['inv:invoiceNumber'];
    this.frmData.invoice_series = object['inv:invoiceSeries'];
    this.frmData.currency_code = object['inv:currencyCode'];
    this.frmData.xml_type = "2";
    if (object['inv:signedDate']) {
      const date = new Date(object['inv:signedDate']);
      if (date.toString() === 'Invalid Date') {
        this.frmData.sign_date = moment(object['inv:signedDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
      } else {
        this.frmData.sign_date = moment(object['inv:signedDate']).format('YYYY-MM-DD hh:mm:ss');
      }
    }
    if (object['inv:invoiceIssuedDate']) {
      const date = new Date(object['inv:invoiceIssuedDate']);
      if (date.toString() === 'Invalid Date') {
        this.frmData.invoice_date = moment(object['inv:invoiceIssuedDate'], 'DD/MM/YYYY').format('YYYY-MM-DD hh:mm:ss');
      } else {
        this.frmData.invoice_date = moment(object['inv:invoiceIssuedDate']).format('YYYY-MM-DD hh:mm:ss');
      }
    }
    this.frmData.seller_tax_code = object['inv:sellerTaxCode'];
    this.frmData.seller_fax_number = object['inv:sellerFaxNumber'];
    this.frmData.seller_contact_name = object['inv:sellerContactPersonName'];
    this.frmData.seller_address = object['inv:sellerAddressLine'];
    this.frmData.seller_mobile = object['inv:sellerPhoneNumber'];
    this.frmData.seller_legal_name = object['inv:sellerLegalName'];
    this.frmData.buyer_legal_name = object['inv:buyerLegalName'];
    this.frmData.buyer_tax_code = object['inv:buyerTaxCode'];
    this.frmData.buyer_mobile = object['inv:buyerPhoneNumber'];
    this.frmData.buyer_email = object['inv:buyerEmail'];
    if (object.hasOwnProperty('inv:exchangeRate') || object.hasOwnProperty('inv:exchangeRate')) {
      if (object['inv:exchangeRate']) {
        this.frmData.currency_rate = object['inv:exchangeRate'].replace(',', '.');
      } else if (object['inv:ExchangeRate']) {
        this.frmData.currency_rate = object['inv:ExchangeRate'].replace(',', '.');
      }
    }
    if (object.hasOwnProperty('inv:sellerBankAccount')) {
      if (object['inv:sellerBankAccount']) {
        this.frmData.seller_bank_account = object['inv:sellerBankAccount'] + ' - ' + object['inv:sellerBankName'];
      } else {
        this.frmData.seller_bank_account = object['inv:sellerBankName'];
      }
    } else {
      this.frmData.seller_bank_account = object['inv:sellerBankName'];
    }
    const payments = object['inv:payments']['inv:payment'][0];
    if (payments.hasOwnProperty('inv:paymentMethodName')) {
      this.frmData.payment_method_view_name = payments['inv:paymentMethodName'][0];
    } else {
      this.frmData.payment_method_view_name = payments['inv:paymentMethodNameExt'][0];
    }
    if (object['inv:sellerEmail']) {
      if (object['inv:sellerEmail'] !== '.') {
        this.frmData.seller_email = object['inv:sellerEmail'];
        this.emails = [];
        const stringDataEmailGet = object['inv:sellerEmail'].split(';');
        for (let i = 0; i < stringDataEmailGet.length; i++) {
          this.emails.push({ display: stringDataEmailGet[i], value: stringDataEmailGet[i] });
        }
      } else {
        this.frmData.seller_email = null;
        this.emails = [];
      }
    } else {
      this.frmData.seller_email = null;
      this.emails = [];
    }
    if (object['inv:items']) {
      const arrItem = object['inv:items']['inv:item'];
      for (let i = 0; i < arrItem.length; i++) {
        for (const keyItem in arrItem[i]) {
          if (arrItem[i].hasOwnProperty(keyItem)) {
            arrItem[i][keyItem] = arrItem[i][keyItem][0].trim();
          }
        }
      }
      const arrItemFormat = this.invoiceBuyService.formatArrayItem1(arrItem);
      arrItemFormat.forEach((value, index) => {
        if (value.quantity) {
          value.quantity = parseInt(value.quantity.replace(',', '.'), 10);
        } else {
          value.quantity = null;
        }
        if (!value.product_code) {
          value.product_code = null;
        }
        if (value.price) {
          value.price = parseInt(value.price.replace(',', '.'), 10);
        } else {
          value.price = null;
        }
        if (value.amount_after_vat) {
          value.amount_after_vat = parseInt(value.amount_after_vat.replace(',', '.'), 10);
        } else {
          value.amount_after_vat = null;
        }
        if (!value.unit_name) {
          value.unit_name = null;
        }
        if (value.amount) {
          value.amount = parseInt(value.amount.replace(',', '.'), 10);
        } else {
          value.amount = null;
        }
        if (value.vat) {
          value.vat = parseInt(value.vat.replace(',', '.'), 10);
        } else {
          value.vat = null;
        }
        if (value.amount_vat) {
          value.amount_vat = parseInt(value.amount_vat.replace(',', '.'), 10);
        } else {
          value.amount_vat = null;
        }
        if (value.unit_code === undefined) {
          value.unit_code = null;
        }
        if (value.amount && value.amount_vat) {
          value.amount_after_vat = value.amount + value.amount_vat;
        } else if (value.amount && !value.amount_vat) {
          value.amount_after_vat = value.amount;
        } else {
          value.amount_after_vat = null;
        }
        value.order = index + 1;
      });
      this.frmData.input_invoice_products = arrItemFormat.filter((item) => {
        if (item.product_name !== 'Tng tin hng' && item.product_name !== 'Tng tin chit khu' && item.product_name !== 'Chit khu') {
          return item;
        }
      });
    }
    this.frmData.amount = parseInt(object['inv:totalAmountWithoutVAT'].replace(',', '.'), 10);
    this.frmData.total_amount_vat = parseInt(object['inv:totalVATAmount'].replace(',', '.'), 10);
    this.frmData.total_payment = parseInt(object['inv:totalAmountWithVAT'].replace(',', '.'), 10);
    this.frmData.total_payment_in_word = object['inv:totalAmountWithVATInWords'];
    this.frmData.input_invoice_files = [{
      order: 'DK',
      file_name: nameFile,
      file_content: dataBase64Code,
      is_file_import: true
    }];
    this.onFieldDataChanged();
  }

  onFieldDataChanged() {
    this.formEvent.emit(this.frmData);
  }

  openFile() {
    document.getElementById('file-id').click();
  }

  checkNumberInvoice() {
    if (VStringService._getValueToString(this.frmData.template_code) == '') {
      Library.notify(this._translate.CONTROLL.LABEL.invoice_form + ' ' + this._translate.VALIDATION.required, 'error');
    } else if (VStringService._getValueToString(this.frmData.invoice_series) == '') {
      Library.notify(this._translate.GRID.INVOICE.invoice_series + ' ' + this._translate.VALIDATION.required, 'error');
    } else if (VStringService._getValueToString(this.frmData.invoice_date) == '') {
      Library.notify(this._translate.GRID.INVOICE.invoice_date + ' ' + this._translate.VALIDATION.required, 'error');
    } else if (VStringService._getValueToString(this.frmData.invoice_number) == '') {
      Library.notify(this._translate.GRID.INVOICE.invoice_number + ' ' + this._translate.VALIDATION.required, 'error');
    } else {
      Library.showloading();
      const _data = {
        input_invoice: {
          template_code: this.frmData.template_code,
          invoice_number: this.frmData.invoice_number,
          invoice_series: this.frmData.invoice_series,
          tax_code: this.frmData.seller_tax_code,
          invoice_date: moment(this.frmData.invoice_date).format('YYYY-MM-DD'),
        }
      };
      this.invoiceBuyService.apiCheckNumberInvocie(_data).subscribe(
        (res: any) => {
          const initialState = {
            isError: res.message.status === 'success' ? false : true,
            message: res.message.content
          };
          this.modalService.show(CheckInvoiceNumberComponent, {
            class: 'modal-md', initialState, backdrop: 'static',
            keyboard: false
          });
        }, error => Library.notify(error, 'error'),
        () => Library.hideloading()
      );

    }

  }

  clearDataFrm() {
    this.myFile.nativeElement.value = '';
    this.frmData = new Invoice('1');
    this.emails = [];
    this.onFieldDataChanged();
    this.frmData.version_xml = "1";
  }
}
