import { Component, OnInit } from '@angular/core';
import { AuthService } from '@core/services';
import { BsModalRef } from 'ngx-bootstrap';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-notify-tax-code',
  templateUrl: './notify-tax-code.component.html',
  styleUrls: ['./notify-tax-code.component.scss']
})
export class NotifyTaxCodeComponent implements OnInit {
  _translate: any;
  _language: string;
  data: any = {};
  isShow: boolean;
  message: string;
  trangthai: any;
  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    public translate: TranslateService
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.translate.use(this._language);
  }

  ngOnInit() {
    switch (this.data.status) {
      case "00":
        this.trangthai = "NNT ang hot ng";
        break;
      case "01":
        this.trangthai = "NNT ngng hot ng v  hon thnh th tc chm dt hiu lc MST";
        break;
      case "02":
        this.trangthai = "NNT  chuyn c quan thu qun l";
        break;
      case "03":
        this.trangthai = "NNT ngng H nhng cha hon thnh th tc chm dt hiu lc MST";
        break;
      case "04":
        this.trangthai = "NNT ang hot ng (cha y  th tc cp MST)";
        break;
      case "05":
        this.trangthai = "NNT tm ngng KD c thi hn";
        break;
      case "06":
        this.trangthai = "NNT khng hot ng ti a ch  ng k";
        break;
    }
  }

  close() {
    this.bsModalRef.hide();
  }

  demo() {
    const win = window.open('http://tracuunnt.gdt.gov.vn/tcnnt/mstdn.jsp', '_blank');
    win.focus();
  }
}
