import { THIS_EXPR } from '@angular/compiler/src/output/output_ast';
import { Component, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { el } from '@angular/platform-browser/testing/src/browser_util';
import { Library } from '@core/library';
import { AuthService } from '@core/services';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { TranslateService } from '@ngx-translate/core';
import { BsModalRef } from 'ngx-bootstrap';
import { collapseTextChangeRangesAcrossMultipleVersions } from 'typescript';
declare var $: any;
import { NgxSpinnerService } from 'ngx-spinner';
import { DxDataGridComponent } from 'devextreme-angular';
import { t } from '@angular/core/src/render3';
import { Router } from '@angular/router';

@Component({
  selector: 'app-list-mail-received',
  templateUrl: './list-mail-received.component.html',
  styleUrls: ['./list-mail-received.component.scss']
})
export class ListMailReceivedComponent implements OnInit {
  dataSource: any = {};
  customizeText: any;
  _translate: any;
  format: any
  isSelected = false;
  showPopupImport = false;
  valueProgress: number = 0;
  selectedRows: Array<any> = [];
  mailID: any;
  _language: string;
  mailStatus: any;
  isShowButtonPanel = false;
  checkBoxValue = false;
  valueProgressMax: number;
  previewEmmail: any;
  isShowProgressBar = false;
  data: any;
  listMailCount = 0;
  subPullMail: any;
  currentUser: any;
  countInvoice = 0;
  is_preview = false;
  listMail = [];
  search: any;
  noReceivedBtn: any;
  receivedBtn: any;
  isMails: any;
  deleteBtn: any;
  newData: any;
  selectedItems: Array<any> = [];
  @Input() isMailReceived;
  @Input() isHasPackage;
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @Output("currentMail") currentMail = new EventEmitter<any>();
  pageN = 'compact';
  constructor(private authService: AuthService,
    public translate: TranslateService,
    private invoiceBuyService: InvoiceBuyService,
    private spinner: NgxSpinnerService,
    private router: Router
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);

  }

  ngOnInit() {

  }
  loadList(params?) {
    this.dataSource = {};
    this.dataSource.store = this.invoiceBuyService.getALlEmail(params)
  }
  getSearch(e) {
    this.loadList(e);
    this.search = e;
  }
  isMail(e) {
    this.isMails = e;
  }
  onRowPrepared(e) {

  }
  styleProgressBar(e) {

  }

  myDropdown(e) {
    if (e.isTrue) {
      this.loadList(this.search);
      this.loadPreviewMail(e.idMail);
      setTimeout(() => {
        this.newData = this.dataGrid.instance.getSelectedRowsData();
      }, 1000);
      setTimeout(() => {
        switch (e.action) {
          case "khong_nhan_hd":
            this.noReceivedBtn = "disabled-btn";
            this.receivedBtn = "";
            this.deleteBtn = "";
            break;
          case "nhan_hd":
            if (this.newData[0].status === 'KHONG_HOP_LE') {
              this.noReceivedBtn = "disabled-btn";
              this.receivedBtn = "";
              this.deleteBtn = "";
            } else {
              this.noReceivedBtn = "";
              this.receivedBtn = "disabled-btn";
              this.deleteBtn = "none-delete";
            }
            break;
          case "":
            break;
          case "":
            break;
        }
      }, 1100);
    }
  }
  loadPreviewMail(id) {
    let filter = `&filter_groups[1][filters][0][key]=id
    &filter_groups[1][filters][0][value]=${id}
    &filter_groups[1][filters][0][operator]=eq&`
    this.invoiceBuyService.getEmailPreview(filter).subscribe(res => {
      if (res.status == "success") {
        this.previewEmmail = res.data.input_invoice_mails[0]
        this.is_preview = true;
      }
      else {
        Library.notify(res.message, 'error')
      }
    })
  }
  handlePropertyChange(e) {
    if (e.value) {
      this.dataGrid.instance.selectAll();
    }
    else {
      this.dataGrid.instance.clearSelection();
    }
  }
  selectionChanged(e) {
    this.selectedItems = e.selectedRowsData;
    if (e.selectedRowsData.length > 0) {
      this.isSelected = true
      const KHONG_HOP_LE = this.selectedItems.filter(item => item.status == 'KHONG_HOP_LE')
      const HOP_LE = this.selectedItems.filter(item => item.status == 'HOP_LE')
      if (e.selectedRowsData.length === 1) {
        let id = e.selectedRowsData[0].id
        this.loadPreviewMail(id)
        if (KHONG_HOP_LE.length > 0) {
          this.noReceivedBtn = "disabled-btn"
        } else {
          this.noReceivedBtn = ""
        }
        if (HOP_LE.length > 0) {
          this.receivedBtn = "disabled-btn"
          this.deleteBtn = "none-delete"
        } else {
          this.receivedBtn = ""
          this.deleteBtn = ""
        }
      }
      else {
        this.is_preview = false
        if (KHONG_HOP_LE.length > 0) {
          this.noReceivedBtn = "disabled-btn"
        } else {
          this.noReceivedBtn = ""
        }
        if (HOP_LE.length > 0) {
          this.receivedBtn = "disabled-btn"
          this.deleteBtn = "none-delete"
        } else {
          this.receivedBtn = ""
          this.deleteBtn = ""
        }
      }
    } else {
      this.is_preview = false;
      this.isSelected = false;
    }
  }
  getMail(e) {
    this.mailID = e;
    let filter = `&filter_groups[1][filters][0][key]=id
      &filter_groups[1][filters][0][value]=${this.mailID}
      &filter_groups[1][filters][0][operator]=eq&`
    this.invoiceBuyService.getEmailAccount(filter).subscribe(res => {
      this.mailStatus = res.data.config_mails[0].status
    })
    this.currentMail.emit(e)
  }
  onImportShowing(e) {
    this.valueProgress = 0;
  }
  onImportHiding(e) {
    let r = true;
    if (this.valueProgress < 100) {
      r = confirm("Cnh bo: Qu trnh np d liu s dng li nu bn ng ca s ny");
    }
    if (r) {
      if (this.subPullMail) {
        this.subPullMail.unsubscribe();
      }
      this.valueProgress = 0;
    } else {
      e.cancel = true;
    }
  }
  pullMail() {
    this.isShowProgressBar = true;
    let percentComplete = 100 / this.valueProgressMax;
    this.subPullMail = this.invoiceBuyService.pullMailByUid(this.listMail[this.countInvoice]).subscribe(res => {
      if (res.status == 'success') {
        this.valueProgress += percentComplete;
        if (this.countInvoice < this.valueProgressMax - 1) {
          this.countInvoice++;
          this.pullMail()
        } else if (this.countInvoice == this.valueProgressMax - 1) {
          Library.notify("Ti thnh cng", "success");
          this.isShowProgressBar = false;
          this.loadList(this.search)
        }
      } else {
        this.isShowProgressBar = false;
        Library.notify(res.message, "error");
      }
      // else {
      //   this.valueProgress = this.countInvoice;
      //   Library.notify(res.message, 'error')
      //   if (this.countInvoice < this.listMailCount) {
      //     this.isShowButtonPanel = true;
      //     this.countInvoice++;
      //   } else if (this.countInvoice == this.listMailCount) {
      //     this.isShowButtonPanel = false;
      //     Library.notify("Qu trnh ti hon tt", "success");
      //     this.subPullMail.unsubscribe();
      //   }
      // }
    })
  }
  convertToPullMail() {
    if (this.isMails) {
      if (this.mailStatus == '1') {
        let mail = {
          config_mail_id: this.mailID
        };
        this.spinner.show();
        this.invoiceBuyService.getUidMail(mail).subscribe(res => {
          if (res.status == "success") {
            let listMailArray = []
            let isEmpty = Object.keys(res.data)
            if (isEmpty.length > 0) {
              this.data = res.data;
              const arrData = Object.keys(this.data)
              arrData.forEach(element => {
                let fieldName = element
                let arrUid = this.data[fieldName]
                arrUid.forEach(item => {
                  let ojb = {
                    config_mail_id: this.mailID,
                    uid: item,
                    folder: fieldName
                  }
                  listMailArray.push(ojb);
                })
              });
              this.listMail = listMailArray;
              this.valueProgressMax = listMailArray.length;
              this.spinner.hide();
              this.pullMail();
            } else {
              this.spinner.hide();
              Library.notify('Khng c th mi', 'error')
            }
          } else {
            this.spinner.hide();
            Library.notify(res.message, "error")

          }
        })
      }
      else {
        this.spinner.hide();
        Library.notify(this._translate.NOTIFY.email_notify, "error")
      }
    } else {
      const result = Library.confirm(this._translate.CONFIRM.email_config, this._translate.NOTIFY.email_config);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.router.navigate(['system/configs', { type: 'mail' }])
        }
      });
    }
  }
  formata = (value) => {
    let percent = Math.round(value * 100);
    return 'Hon thnh: ' + percent + '%';
  }
  deleteMail(id) {
    let deleteItem = this.dataGrid.instance.getSelectedRowsData();
    let arrId = [];
    let ojb = {};
    if (id) {
      arrId.push(id)
      if (this.isMailReceived.listDelete) {
        ojb = {
          "soft_del": true,
          "ids": arrId
        }
      } else {
        ojb = {
          "soft_del": false,
          "ids": arrId
        }
      }
      const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.invoiceBuyService.deleteMailId(ojb).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify(this._translate.NOTIFY.delete_success, 'success');
              this.loadList(this.search);
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    } else {
      for (let i = 0; i < deleteItem.length; i++) {
        arrId.push(deleteItem[i].id)
      }
      if (this.isMailReceived.listDelete) {
        ojb = {
          "soft_del": true,
          "ids": arrId
        }
      } else {
        ojb = {
          "soft_del": false,
          "ids": arrId
        }
      }
      if (deleteItem.length === 0) {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      } else {
        const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
        result.then((dialogResult) => {
          if (dialogResult) {
            Library.showloading();
            this.invoiceBuyService.deleteMailId(ojb).subscribe(rs => {
              if (rs.status === 'success') {
                Library.notify(this._translate.NOTIFY.delete_success, 'success');
                this.loadList(this.search);
              } else {
                Library.notify(rs.message, 'error');
              }
            }, err => {
            }, () => {
              Library.hideloading();
            });
          }
        });
      }
    }
  }

  noReceived(id) {
    if (id) {
      let arrId = [];
      arrId.push(id)
      const result = Library.confirm(this._translate.CONFIRM.definitely_do_not_receive, this._translate.NOTIFY.notify);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          let obj = {
            ids: arrId,
            error_reason: "Ngi dng  chuyn trng thi email thnh khng hp l",
          }
          this.invoiceBuyService.notReceived(obj).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify(this._translate.NOTIFY.finish, 'success');
              this.noReceivedBtn = "disabled-btn";
              this.receivedBtn = "";
              this.deleteBtn = "";
              this.loadList(this.search);
              this.loadPreviewMail(arrId[0])
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    } else {
      let deleteItem = this.dataGrid.instance.getSelectedRowsData();
      let arrId = [];
      for (let i = 0; i < deleteItem.length; i++) {
        arrId.push(deleteItem[i].id)
      }
      if (deleteItem.length === 0) {
        Library.notify('Cha bn ghi no c chn', 'error');
      } else {
        const result = Library.confirm(this._translate.CONFIRM.definitely_do_not_receive, this._translate.NOTIFY.notify);
        result.then((dialogResult) => {
          if (dialogResult) {
            Library.showloading();
            let obj = {
              ids: arrId,
              error_reason: "Ngi dng  chuyn trng thi email thnh khng hp l",
            }
            this.invoiceBuyService.notReceived(obj).subscribe(rs => {
              if (rs.status === 'success') {
                Library.notify(this._translate.NOTIFY.finish, 'success');
                this.loadList(this.search);
                if (arrId.length === 1) {
                  this.noReceivedBtn = "disabled-btn";
                  this.receivedBtn = "";
                  this.deleteBtn = "";
                  this.loadPreviewMail(arrId[0])
                }
              } else {
                Library.notify(rs.message, 'error');
              }
            }, err => {
            }, () => {
              Library.hideloading();
            });
          }
        });
      }
    }
  }
  restore(id) {
    if (id) {
      let arrId = [];
      arrId.push(id)
      const result = Library.confirm(this._translate.CONFIRM.restore, this._translate.NOTIFY.notify);
      result.then((dialogResult) => {
        if (dialogResult) {
          Library.showloading();
          this.invoiceBuyService.restore(arrId).subscribe(rs => {
            if (rs.status === 'success') {
              Library.notify(this._translate.NOTIFY.restore, 'success');
              this.loadList(this.search);
            } else {
              Library.notify(rs.message, 'error');
            }
          }, err => {
          }, () => {
            Library.hideloading();
          });
        }
      });
    } else {
      let deleteItem = this.dataGrid.instance.getSelectedRowsData();
      let arrId = [];
      for (let i = 0; i < deleteItem.length; i++) {
        arrId.push(deleteItem[i].id)
      }
      if (deleteItem.length === 0) {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      } else {
        const result = Library.confirm(this._translate.CONFIRM.restore, this._translate.NOTIFY.notify);
        result.then((dialogResult) => {
          if (dialogResult) {
            Library.showloading();
            this.invoiceBuyService.restore(arrId).subscribe(rs => {
              if (rs.status === 'success') {
                Library.notify(this._translate.NOTIFY.restore, 'success');
                this.loadList(this.search);
              } else {
                Library.notify(rs.message, 'error');
              }
            }, err => {
            }, () => {
              Library.hideloading();
            });
          }
        });
      }
    }
  }
  nhanHoaDon(data?) {
    if (this.isHasPackage) {
      this.selectedItems = this.dataGrid.instance.getSelectedRowsData();
      if (this.selectedItems.length || data) {
        const result = Library.confirm(this._translate.CONFIRM.would_you_like_to_receive_this_invoice, this._translate.NOTIFY.notify);
        result.then((dialogResult) => {
          if (dialogResult) {
            let ids = [];
            let isKhongNhan = false;
            if (data) {
              if (data.status == 'HOP_LE')
                isKhongNhan = true;
              else
                ids.push(data.id);
            }
            else {
              let lstThuHopLe = this.selectedItems.filter(p => p.status == 'HOP_LE');
              if (lstThuHopLe.length == this.selectedItems.length)
                isKhongNhan = true;
              else {
                let lstThuKhac = this.selectedItems.filter(p => p.status != 'HOP_LE');
                ids = lstThuKhac.map(e => e.id);
              }
            }
            if (isKhongNhan) {
              Library.notify(this._translate.NOTIFY.you_cannot_do_this_for_valid_email, 'error');
            }
            else {
              Library.showloading();
              this.invoiceBuyService.nhanHoaDon(ids).subscribe(res => {
                if (res.status == 'success') {
                  Library.notify(this._translate.NOTIFY.receive_invoice_success, 'success');
                  this.loadList(this.search);
                  if (ids.length === 1) {
                    setTimeout(() => {
                      this.newData = this.dataGrid.instance.getSelectedRowsData();
                    }, 1000);
                    setTimeout(() => {
                      if (this.newData[0].status === 'KHONG_HOP_LE') {
                        this.noReceivedBtn = "disabled-btn";
                        this.receivedBtn = "";
                        this.deleteBtn = "";
                      } else {
                        this.noReceivedBtn = "";
                        this.receivedBtn = "disabled-btn";
                        this.deleteBtn = "none-delete";
                      }
                    }, 1100);
                    this.loadPreviewMail(ids[0])
                  }
                } else {
                  Library.notify(res.message, 'error');
                }
              }, err => {
              }, () => {
                Library.hideloading();
              });
            }
          }
        });
      }
      else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }
    } else {
      Library.notify("Vui lng mua gi ha n u vo  s dng chc nng ny", "error")
    }
  }
}
