import { Pipe } from '@angular/core';
@Pipe({ name: 'diaryType' })
export class InvoicePipe {
    transform(type_code: string) {
        switch (type_code) {
            case "HOP_LE":
                return "Hp l";
            case "KHONG_HOP_LE":
                return "Khng hp l";
            case "CHUA_NHAN":
                return "Cha nhn";
            default:
                break;
        }
    }
}