import {Injectable} from '@angular/core';
import {ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot} from '@angular/router';
import {Observable} from 'rxjs';
import {Library} from '@core/library';
import {InvoiceBuyService} from '@modules/invoice-buy/invoice-buy.service';
import {finalize, map} from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class FormResolverService implements Resolve<any[]> {
  idInvoice: string = null;

  constructor(
    private invoiceBuyService: InvoiceBuyService,
    private router: Router,
  ) {
  }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any[]> | Promise<any[]> | any[] {
    this.idInvoice = route.paramMap.get('id');
    Library.showloading();
    return this.invoiceBuyService.formResolveApi(this.idInvoice)
      .pipe(
        map(value => {
          //
          if (value.length > 3) {
            if (value[3].status === 'success') {
              return [
                value[0].data,
                value[1].data,
                value[2].data.products,
                value[3].data,
              ];
            } else {
              this.router.navigate(['/']);
            }
          } else {
            return [
              value[0].data,
              value[1].data,
              value[2].data.products,
            ];
          }
        }),
        finalize(() => Library.hideloading())
      );
  }
}
