import { Component, OnInit, ViewChild } from '@angular/core';
import { AuthService } from '@core/services';
import { VatService } from '@modules/vat/shared';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { SocketService, sEnum } from '@core/index';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';

@Component({
  selector: 'app-index',
  templateUrl: './index.component.html',
  styleUrls: ['./index.component.scss']
})
export class IndexComponent implements OnInit {
  _language: string;
  _translate: any;
  tabs = [];
  isTabHopThuDen = false;
  isTabHoaDonDauVao = false;
  isTabThuDaXoa = false;
  isTabHoaDonTCT = false;
  isMailReceived: any;
  currentMailId: any;
  isHasPackage = false;
  configs: any;
  selectedIndex: any;
  constructor(
    private authService: AuthService,
    public bsModalDocumentCancel: BsModalRef,
    public translate: TranslateService,
    public invoiceBuyService: InvoiceBuyService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
    this.configs = this.authService.getConfigs();
  }

  ngOnInit() {
    if (this.configs.system_parameter.is_use_mail_get_invoice || this.configs.system_parameter.is_use_mail_get_invoice == undefined) {
      this.tabs = VSelectBoxService.tabsInvoiceBuyEmail(this._translate);
      this.selectedIndex = this.tabs.length - 1
    } else {
      this.tabs = VSelectBoxService.tabsInvoiceBuyTCT(this._translate);
      this.selectedIndex = this.tabs.length - 1
    }
    this.isTabHoaDonDauVao = true;
    this.invoiceBuyService.getPackage().subscribe(res => {
      if (res.data.meta.total > 0) {
        this.isHasPackage = true;
      } else {
        this.isHasPackage = false;
      }
    })
  }
  ngOnDestroy() {
  }
  getCurrentMail(e) {
    this.currentMailId = e
  }
  async selectTab(e) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.hoadonmuavao);
    if (!isCheckInitFee)
      return;
    const tab = e.itemData.code;
    switch (tab) {
      case 'HOP_THU_DEN':
        this.isTabHopThuDen = true;
        this.isTabHoaDonDauVao = false;
        this.isTabThuDaXoa = false;
        this.isTabHoaDonTCT = false;
        this.isMailReceived = { listDelete: true, id: this.currentMailId };
        break;
      case 'HOA_DON_DAU_VAO':
        this.isTabHopThuDen = false;
        this.isTabHoaDonDauVao = true;
        this.isTabThuDaXoa = false;
        this.isTabHoaDonTCT = false;
        break;
      case 'THU_DA_XOA':
        this.isTabHopThuDen = false;
        this.isTabHoaDonDauVao = false;
        this.isTabThuDaXoa = true;
        this.isTabHoaDonTCT = false;
        this.isMailReceived = { listDelete: false, id: this.currentMailId };
        break;
      case 'HOA_DON_DAU_VAO_TCT':
        this.isTabHopThuDen = false;
        this.isTabHoaDonDauVao = false;
        this.isTabThuDaXoa = false;
        this.isTabHoaDonTCT = true;
        this.isMailReceived = { listDelete: false, id: this.currentMailId };
        break;
      default:
        break;
    }
  }
}
