import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { AuthService } from '@core/services';
import { VatService } from '@modules/vat/shared';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { DxDataGridComponent, DxFilterBuilderComponent, DxMenuComponent } from 'devextreme-angular';
import { Library } from '@core/library';
import { Helper } from '@core/helper/Helper';
import { VPreviewInvService } from '@core/library/invoice/v-preview-inv.service';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { UploadFileZipComponent } from '@modules/invoice-buy/shared/modal/upload-file-zip/upload-file-zip.component';
import { empty } from 'rxjs';
import { SocketService } from '@core/index';
import { Subscription } from 'rxjs/Subscription';
import { ViewInvoiceMultipageComponent } from '@shared/index';
import { Invoice } from '@modules/invoice-buy/shared/class/invoice.model';
import { NgxSpinnerService } from 'ngx-spinner';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { CaptchaUpdateComponent } from '../captcha-update/captcha-update.component';
import { linkToolbarAction } from '@syncfusion/ej2-angular-richtexteditor';
import { ConfigsService } from '@modules/configs/shared';
import { collapseTextChangeRangesAcrossMultipleVersions } from 'typescript';
import { extractStyleParams } from '@angular/animations/browser/src/util';
import * as moment from 'moment';

@Component({
  selector: 'app-list-invoice-tct',
  templateUrl: './list-invoice-tct.component.html',
  styleUrls: ['./list-invoice-tct.component.scss']
})
export class ListInvoiceTCTComponent implements OnInit {
  @ViewChild(DxMenuComponent) dxMenu: DxMenuComponent;
  @Input() isHasPackage;
  @ViewChild('expandMenu') expandMenu;
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @ViewChild(DxFilterBuilderComponent) filterBuilder: DxFilterBuilderComponent;
  // dataSource: DataSource;
  dataSource: any;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  bsModalSignRef: BsModalRef;
  bsModalRef: BsModalRef;
  listDM: any;
  loaiHoaDon: string;
  test = true;
  isReceiveInvoice = false;
  pagereceiveInvoice: any;
  statusInvoice: any = {};
  isAccessExportInv = true;
  invoice_releases = [];
  paramSearch: string = null;
  _language: string;
  _translate: any;
  checkSubmit: any;
  extras = [];
  data_extras_invoice_buy = [];
  extend = [];
  select: any = {};
  adjustmentTypes: any = []
  testResults: any = [];
  configs: any;
  disEditMulti = true;
  system_parameter: any = {};
  clickCurent = '';
  showStatus = false;
  isSearchUI = true;
  dataAddInvoiceBuy = [];
  paramsExportData: string = null;
  ioSignature: Subscription;
  frmData: Invoice = new Invoice('1');
  statusFilter: any;
  exportSchedulesTCT: any = [];
  currentPageNumber = 1;
  pageNumber: number = 1;
  listState: any = [];
  isPagination = false;
  totalInvoice: number = 0;
  objSearch: any = {
    endDate: '',
    contentSearch: '',
    test_results: '5',
    startDate: '',
    exportSchedulesDefault: 'THANG_NAY',
    limit: 15,
    page: 1,
    state: null,
  };
  startDate: any;
  minDate: any;
  maxDate: any;
  endDateTCT: any;
  placeholder: any;
  pagination = [
    { name: 15 },
    { name: 30 },
    { name: 50 },
  ];
  constructor(
    private authService: AuthService,
    private vatService: VatService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    public bsModalDocumentCancel: BsModalRef,
    private router: Router,
    private route: ActivatedRoute,
    public translate: TranslateService,
    private invoiceBuyService: InvoiceBuyService,
    private socketService: SocketService,
    private configsService: ConfigsService
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    this.adjustmentTypes = VSelectBoxService._adjustmentTypesTCT(this._translate);
    this.testResults = VSelectBoxService._results(this._translate);
    this.exportSchedulesTCT = Helper.getExportScheduleTCT(this._translate).schedule;
    translate.use(this._language);
    this.data_extras_invoice_buy = [{
      id: '1',
      name: this._translate.CONTROLL.LABEL.export_data,
      items: [
        {
          id: 'LIST_INVOICE_BUY',
          name: this._translate.CONTROLL.LABEL.list_invoice_buy,
          icon: this._translate.ICONS.excel
        },
        {
          id: 'DETAILS_OF_EACH_GOODS_AND_SERVICE',
          name: this._translate.CONTROLL.LABEL.details_of_each_goods_and_service,
          icon: this._translate.ICONS.edit
        },
      ]
    }];
    this.dataAddInvoiceBuy = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.add,
      items: [
        {
          id: 'ADD_ONE_INVOICE',
          name: this._translate.CONTROLL.LABEL.add_one_invoice,
          icon: this._translate.ICONS.add_new
        },
        {
          id: 'ADD_MULTIPLE_INVOICE',
          name: this._translate.CONTROLL.LABEL.add_multiple_invoice,
          icon: this._translate.ICONS.add_new
        },
      ]
    }];
    this.placeholder =
      this._translate.CONTROLL.LABEL.input +
      " " +
      this._translate.GRID.INVOICE.invoice_number +
      ", " +
      this._translate.CONTROLL.LABEL.tax_code;
  }

  ngOnInit() {
    this.connectWebsocket();
    let oKyXuat = this.exportSchedulesTCT.filter(
      (p) => p.code == "THANG_NAY"
    )[0];
    if (oKyXuat) {
      this.objSearch.startDate = oKyXuat.startDate;
      this.objSearch.endDate = oKyXuat.endDate;
      this.minDate = oKyXuat.startDate;
      this.maxDate = oKyXuat.endDate;
    }
  }
  ngOnDestroy() {
    this.ioSignature.unsubscribe();
    this.isSearchUI = false;
  }
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
    });
  }
  getSearch(e) {
    this.paramSearch = e;
    // this.loadList(e);
  }

  itemExtrasClick(e) {
    if (e.itemData.id === 'ADD_ONE_INVOICE') {
      if (this.isHasPackage) {
        this.router.navigate(['/system/invoice-buy/create']);
      } else {
        Library.notify("Vui lng mua gi ha n u vo  s dng chc nng ny", "error")
      }
    } else if (e.itemData.id === 'ADD_MULTIPLE_INVOICE') {
      if (this.isHasPackage) {
        this.importMultipleFile();
      } else {
        Library.notify("Vui lng mua gi ha n u vo  s dng chc nng ny", "error")
      }
    } else if (e.itemData.id === 'LIST_INVOICE_BUY') {
      this.exportXLXS();
    } else if (e.itemData.id === 'DETAILS_OF_EACH_GOODS_AND_SERVICE') {
      this.exportDetails();
    }
  }

  // xu ly cho export XML chua xong
  setParameExport() {
    let dataValue;
    const sort = '&sort[0][key]=invoice_date&sort[0][direction]=DESC';
    const period = this.route.snapshot.queryParams.exportSchedulesDefault;
    if (period === 'TAT_CA') {
      dataValue = period + this.paramsExportData + sort;
    } else {
      const from_date = this.route.snapshot.queryParams.startDate;
      const to_date = this.route.snapshot.queryParams.endDate;
      dataValue = `${period}&options[from_date]=${from_date}&options[to_date]=${to_date}` + this.paramsExportData + sort;
    }
    return dataValue;
  }

  exportXLXS() {
    Library.showloading();
    this.invoiceBuyService.apiExportListInvoicebuy(this.setParameExport())
      .subscribe(res => {
        if (res.status === 'success') {
          const file = res.data.report;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
          Library.notify('Xut file Excel thnh cng', 'success');
        } else {
          Library.notify(res.message, 'error');
        }
      }, error => Library.notify(error, 'error'),
        () => Library.hideloading()
      );
  }

  exportDetails() {
    Library.showloading();
    this.invoiceBuyService.apiExportDetailInvoicebuy(this.setParameExport())
      .subscribe(res => {
        if (res.status === 'success') {
          const file = res.data.report;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
          Library.notify('Xut file thnh cng', 'success');
        } else {
          Library.notify(res.message, 'error');
        }
      }, error => Library.notify(error, 'error'),
        () => Library.hideloading()
      );
  }

  importMultipleFile() {
    this.bsModalDocumentCancel = this.modalService.show(UploadFileZipComponent, {
      class: 'modal-lg full-modal', backdrop: 'static',
      keyboard: false
    });

    this.modalService.onHide.subscribe(
      () => {
        if (this.bsModalDocumentCancel.content.LoadList === 'LoadList') {
          // this.loadList();
          delete this.bsModalDocumentCancel.content.LoadList;
        }
      });
  }

  edit(data) {
    const row = data ? data : this.selectedItems[0];
    this.router.navigate([`/system/invoice-buy/edit-invoice/${row.id}`]);
  }

  inChuyenDoi(data) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    if (this.selectedItems.length || data) {
      const _lst = this.selectedItems.filter(p => p.status === 'DA_XUAT');
      if (_lst.length > 0 || data) {
        let ids = [];
        if (data) {
          if (data.status === 'DA_XUAT') {
            ids.push(data.id);
          } else {
            Library.notify(this._translate.NOTIFY.not_print_convert, 'error');
            return;
          }
        } else {
          ids = _lst.map(e => e.id);
        }
        let w = 1920, h = 900;
        w = window.screen.availWidth * 100 / 100;
        h = window.screen.availHeight * 93 / 100;
        const win = window.open('about:blank', 'name', 'width=200, height=100');
        win.document.body.innerHTML = this._translate.NOTIFY.loading;
        win.blur();
        Library.showloading();
        const sub = this.vatService.inChuyenDoiInvoiceImport(ids, false).subscribe(rs => {
          if (rs.status === 'success') {
            const pdf_file = rs.data.invoice.pdf_file;
            const objectUrl = VPreviewInvService._getUrlInvoice(pdf_file);
            win.resizeTo(screen.availWidth, screen.availHeight);
            win.focus();
            win.open(objectUrl, 'name', 'width=' + w + ', height=' + h);
          } else {
            win.close();
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
          sub.unsubscribe();
        });
      } else {
        Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
      }

    } else {
      Library.notify(this._translate.NOTIFY.no_record_selected, 'error');
    }
  }

  delete(data?) {
    if (this.selectedItems.length || data) {
      let objData = {};
      const ids = [];
      const idsEmail = [];
      const isInvoiveInMail = this.selectedItems.filter(item => item.input_invoice_mail_id !== null)
      if (data) {
        ids.push(data.id);
        objData = {
          ids: data.input_invoice_mail_id,
          error_reason: "Ngi dng  xa ha n t danh sch ha n u vo",
        }
      } else {
        this.selectedItems.map((item) => {
          if (item.export_from_ihd !== '1') {
            ids.push(item.id);
            if (item.input_invoice_mail_id) {
              idsEmail.push(item.input_invoice_mail_id)
            }
          }
        });
      }
      if (Array.isArray(ids) && ids.length) {
        const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
        result.then((dialogResult) => {
          if (dialogResult) {
            Library.showloading();
            this.invoiceBuyService.deleteInvoiceId(ids).subscribe(rs => {
              if (rs.status === 'success') {
                if (data) {
                  if (data.input_invoice_mail_id) {
                    let ids = [];
                    ids.push(data.input_invoice_mail_id)
                    objData = {
                      ids: ids,
                      error_reason: "Ngi dng  xa ha n t danh sch ha n u vo",
                    }
                    this.invoiceBuyService.notReceived(objData).subscribe()
                  }
                } if (isInvoiveInMail.length > 0) {
                  objData = {
                    ids: idsEmail,
                    error_reason: "Ngi dng  xa ha n t danh sch ha n u vo",
                  }
                  this.invoiceBuyService.notReceived(objData).subscribe()
                }
                Library.notify(this._translate.NOTIFY.delete_success, 'success');
                // this.loadList();
              } else {
                Library.notify(rs.message, 'error');
              }
            }, err => {
            }, () => {
              Library.hideloading();
            });
          }
        });
      } else {
        Library.notify('Khng c xa ha n t phn mm ihoadon', 'error');
      }
    }
  }

  customizeText = (e) => {
    if (this.invoiceBuyService && this.invoiceBuyService.handleConnect()) {
      const error = this.invoiceBuyService.handleConnect()
      if (error.status == "error") {
        if (error.message == 'Phin ng nhp TCT  ht hn') {
          const initialState = {
            is_expired: true,
          };
          this.bsModalSignRef = this.modalService.show(CaptchaUpdateComponent, { class: 'modal-md', backdrop: 'static', keyboard: false, initialState });
          this.modalService.onHide.subscribe(() => {
          });
        } else {
          if (error.code == 'IWE285') {
            Library.notify(error.message, 'error', 5000);
          }
          else {
            const initialState = {
              is_expired: false,
            };
            this.bsModalSignRef = this.modalService.show(CaptchaUpdateComponent, { class: 'modal-md', backdrop: 'static', keyboard: false, initialState });
            this.modalService.onHide.subscribe(() => {
            });
          }
        }
      } else {
        this.pageNumber = Math.ceil(error.total / this.objSearch.limit);
        this.objSearch.state = error.state;
        this.totalInvoice = error.total;
        if(error.changeFilters) {
          this.currentPageNumber = 1;
          this.listState = [];
          this.objSearch.page = 1;
          this.loadList(false);
        }
        if (this.currentPageNumber < this.pageNumber) {
          document.getElementById('next').classList.add('next-enable')
          document.getElementById('next').removeAttribute('disabled')
        } else {
          document.getElementById('next').classList.remove('next-enable')
          document.getElementById('next').setAttribute('disabled', '')
        }
        if (this.currentPageNumber > 1) {
          document.getElementById('prev').classList.add('prev-enable')
          document.getElementById('prev').removeAttribute('disabled')
        } else {
          document.getElementById('prev').classList.remove('prev-enable')
          document.getElementById('prev').setAttribute('disabled', '')
        }
      }
    }
    return null;
  }

  selectionChanged(data: any) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    this.disEditMulti = true;
    this.selectedItems = data.selectedRowsData;
    if (this.selectedItems.length > 0) {
      const checks = this.selectedItems.filter((item) => {
        return item.number_of_vouchers;
      });
      const checkDaXuat = this.selectedItems.filter((item) => {
        return item.status === 'DA_XUAT';
      });
      if (checks.length === this.selectedItems.length && checkDaXuat.length === 0) {
        this.disEditMulti = false;
      }
    }
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.invoice_number) {
            e.rowElement.style.color = 'red';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        const eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }


  loadList(e) {
    if (e) {
      if (this.isReceiveInvoice) {
        this.objSearch.state = this.listState[this.listState.length - 1];
        this.isReceiveInvoice = false;
      } else {
        this.objSearch.state = this.listState[this.listState.length - 1];
        this.objSearch.page = this.currentPageNumber;
      }
      this.dataSource = {};
      this.dataSource.store = this.invoiceBuyService.searchInvoiceTCT(this.objSearch);
    }
    else {
      this.objSearch.state = null;
      this.currentPageNumber = 1;
      this.dataSource = {};
      this.dataSource.store = this.invoiceBuyService.searchInvoiceTCT(this.objSearch);
    }
    this.dataGrid.instance.clearSelection();
  }
  view(data) {
    const row = data ? data : this.selectedItems[0];
    let dataInvoice = {
      khhdon: row.khhdon,
      khmshdon: row.khmshdon,
      nbmst: row.nbmst,
      shdon: row.shdon,
      type: 'html'
    }
    Library.showloading();
    let initialState = {
      typeAction: 'HOA_DON_TCT',
      dataInvoiceTCT: dataInvoice
    };
    this.bsModalRef = this.modalImportService.show
      (
        ViewInvoiceMultipageComponent,
        {
          backdrop: 'static',
          keyboard: false,
          class: 'modal-xl view-invoice',
          initialState
        }
      );
    const subModal = this.modalImportService.onHide.subscribe(() => {
      subModal.unsubscribe();
    });
    Library.hideloading();
  }
  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  search(data) {
    if (data.export_from_ihd == '1') {
      const win = window.open('https://ihoadon.vn/kiem-tra/?shd=' + data.invoice_number + "&mtc=" + data.lookup_code, '_blank');
      win.focus();
    }
    else {
      if (data.lookup_link) {
        const win = window.open(data.lookup_link, '_blank');
        win.focus();
      }
      else
        Library.notify("Ha n cha nhp link tra cu. Vui lng kim tra li", 'error');
    }
  }
  receiveInvoice() {
    if (this.selectedItems.length > 0) {
      this.configsService.getConfigSearch().subscribe(res => {
        if (res.status == 'success') {
          const result = Library.confirm("Bn  nhn ha n " + res.data.sync_number + "/3" + " ln trong ngy. Bn c mun nhn ha n ny?", this._translate.NOTIFY.notify);
          let data = {
            datas: this.selectedItems
          }
          result.then((dialogResult) => {
            if (dialogResult) {
              Library.showloading();
              this.invoiceBuyService.receiveInvoice(data).subscribe(res => {
                if (res.status === 'success') {
                  this.pagereceiveInvoice = this.objSearch.state;
                  this.isReceiveInvoice = true;
                  Library.notify('Nhn ha n thnh cng', 'success');
                  this.loadList(true)
                } else {
                  Library.notify(res.message, 'error');
                }
              }, error => {
                Library.notify(error, 'error');
              }, () => {
                Library.hideloading();
              }
              )
            }
          });
        }
      })
    } else {
      Library.notify(" Vui lng chn ha n", 'error');
    }
  }
  exportExcel() {
    let data = this.invoiceBuyService.getDataExportExcel()
    this.invoiceBuyService.exportExcel(data).subscribe(res => {
      if (res.status === 'success') {
        const file = res.data;
        Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
        Library.notify('Xut file Excel thnh cng', 'success');
      } else {
        Library.notify(res.message, 'error');
      }
      Library.hideloading();
    });
  }
  checkStatusIhd(data) {
    Library.showloading();
    let dataInvoice = {
      datas: [
        {
          nbmst: data.nbmst,
          khmshdon: data.khmshdon,
          khhdon: data.khhdon,
          shdon: data.shdon,
          id: data.id
        }
      ]
    }
    this.invoiceBuyService.checkInputInvoice(dataInvoice).subscribe(res => {
      if (res.status == 'success') {
        this.showStatus = true;
        this.statusInvoice = res.data.invoices[0]
      } else {
        Library.notify(res.message, 'error')
      }
    }, error => {
      Library.notify(error, 'error');
    }, () => {
      Library.hideloading();
    })
  }
  open() {
    this.bsModalSignRef = this.modalService.show(CaptchaUpdateComponent, { class: 'modal-md', backdrop: 'static', keyboard: false });
  }
  onExportSchedules(e) {
    const selectedItem = e.component.option("selectedItem");
    this.objSearch.exportSchedulesDefault = selectedItem.code;
    this.objSearch.startDate = selectedItem.startDate;
    this.objSearch.endDate = selectedItem.endDate;
  }
  onEndDateTCT(e) {
    if (e.event) {
      const date = new Date(e.value);
      const day = date.getDate();
      const month = date.getMonth() + 1;
      const year = date.getFullYear()
      this.objSearch.endDate = e.value
      let startDate = new Date(this.objSearch.startDate)
      let countDate = (date.getTime() - startDate.getTime()) / (24 * 3600 * 1000);
      if (month == 1 || month == 3 || month == 5 || month == 7 || month == 8 || month == 10 || month == 12) {
        if (countDate > 31 || countDate < 0) {
          this.minDate = date.getTime() - (24 * 3600 * 1000 * 30)
          this.objSearch.startDate = date.getTime() - (24 * 3600 * 1000 * 30);
        }
      } else if (month == 2) {
        if (year % 100 == 0 && year % 400 == 0) {
          if (countDate > 29 || countDate < 0) {
            this.minDate = date.getTime() - (24 * 3600 * 1000 * 28)
            this.objSearch.startDate = date.getTime() - (24 * 3600 * 1000 * 28);
          }
        } else if (year % 4 == 0) {
          if (countDate > 29 || countDate < 0) {
            this.minDate = date.getTime() - (24 * 3600 * 1000 * 28)
            this.objSearch.startDate = date.getTime() - (24 * 3600 * 1000 * 28);
          }
        } else {
          if (countDate > 28 || countDate < 0) {
            this.minDate = date.getTime() - (24 * 3600 * 1000 * 27)
            this.objSearch.startDate = date.getTime() - (24 * 3600 * 1000 * 27);
          }
        }
      } else {
        if (countDate > 30 || countDate < 0) {
          this.minDate = date.getTime() - (24 * 3600 * 1000 * 29)
          this.objSearch.startDate = date.getTime() - (24 * 3600 * 1000 * 29);
        }
      }
    }
  }
  onStartDateTCT(e) {
    if (e.event) {
      const date = new Date(e.value)
      const day = date.getDate();
      const month = date.getMonth() + 1;
      const year = date.getFullYear();
      const today = new Date(moment().endOf('day').format('YYYY-MM-DD HH:mm:ss'))
      let endDate = new Date(this.objSearch.endDate)
      this.objSearch.startDate = e.value;
      let countDate = (endDate.getTime() - date.getTime()) / (24 * 3600 * 1000);
      if (month == 1 || month == 3 || month == 5 || month == 7 || month == 8 || month == 10 || month == 12) {
        if (countDate > 31 || countDate < 0) {
          this.minDate = e.value
          this.objSearch.endDate = date.getTime() + (24 * 3600 * 1000 * 30);
        } else {
          this.minDate = e.value
        }
      } else if (month == 2) {
        if (year % 100 == 0 && year % 400 == 0) {
          if (countDate > 29 || countDate < 0) {
            this.minDate = e.value
            this.objSearch.endDate = date.getTime() + (24 * 3600 * 1000 * 28);
          }
          else {
            this.minDate = e.value
          }
        } else if (year % 4 == 0) {
          if (countDate > 29 || countDate < 0) {
            this.minDate = e.value
            this.objSearch.endDate = date.getTime() + (24 * 3600 * 1000 * 28);
          } else {
            this.minDate = e.value
          }
        } else {
          if (countDate > 28 || countDate < 0) {
            this.minDate = e.value
            this.objSearch.endDate = date.getTime() + (24 * 3600 * 1000 * 27);
          } else {
            this.minDate = e.value
          }
        }
      } else {
        if (countDate > 30 || countDate < 0) {
          this.minDate = e.value
          this.objSearch.endDate = date.getTime() + (24 * 3600 * 1000 * 29);
        } else {
          this.minDate = e.value
        }
      }
    }
  }
  onTestResultsChanged(e) {
    this.objSearch.test_results = e.value;
  }
  onContentSearchChange(e) {
    if (e.event) {
      this.objSearch.contentSearch = e.value
    }
  }
  paginationChange(e) {
    this.objSearch.limit = e.value;
    this.loadList(false)
  }
  next() {
    document.getElementById('next').classList.remove('next-enable')
    document.getElementById('next').setAttribute('disabled', '')
    document.getElementById('prev').classList.remove('prev-enable')
    document.getElementById('prev').setAttribute('disabled', '')
    this.listState.push(this.objSearch.state)
    if (this.currentPageNumber <= this.pageNumber) {
      this.currentPageNumber++
    }
    this.loadList(true);
  }
  prev() {
    document.getElementById('next').classList.remove('next-enable')
    document.getElementById('next').setAttribute('disabled', '')
    document.getElementById('prev').classList.remove('prev-enable')
    document.getElementById('prev').setAttribute('disabled', '')
    this.objSearch.state = this.listState[this.listState.length - 2]
    this.listState.pop();
    if (this.currentPageNumber >= 1) {
      this.currentPageNumber--
    }
    this.loadList(true);
  }
}
