import { Component, OnInit } from '@angular/core';
import { AuthService, CoreService } from '@core/services';
import { BsModalRef } from 'ngx-bootstrap';
import { Router } from '@angular/router';
import { Library } from '@core/library';
import { ConfigsService } from '@modules/configs/shared';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-captcha-update',
  templateUrl: './captcha-update.component.html',
  styleUrls: ['./captcha-update.component.scss']
})
export class CaptchaUpdateComponent implements OnInit {
  _translate: any;
  captcha: any = {};
  isCheck = false;
  tax_code: number;
  arrCookie = [];
  valueCokie: number;
  captcha_code: any
  captchaContent: any;
  is_expired: any;

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    public coreService: CoreService,
    public router: Router,
    public configsService: ConfigsService,
    private domSanitizer: DomSanitizer
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
    this.getCaptcha();
  }

  close() {
    this.bsModalRef.hide();
  }

  dongy() {
    this.bsModalRef.content.successSaveItem = 'SuccessHomePage';
    this.bsModalRef.hide();
  }
  goToConfig() {
    this.router.navigate(['/system/configs', { type: 'tct' }]);
    this.bsModalRef.hide();
  }
  getCaptcha() {
    Library.showloading()
    this.configsService.getCaptcha().subscribe(res => {
      if (res.status == 'success') {
        this.captchaContent = this.domSanitizer.bypassSecurityTrustUrl('data:image/svg+xml;base64,' + res.data.content);
        this.captcha_code = res.data.key;
      } else {
        Library.notify('H thng ca tng cc thu ang li, vui lng th li', 'error', 5000)
      }
      Library.hideloading()
    })
  }
  udateCaptcha() {
    Library.showloading();
    let data = {
      config_search: {
        captcha_key: this.captcha_code,
        captcha_code: this.captcha.captcha_code
      }
    }
    this.configsService.updateCaptcha(data).subscribe(res => {
      if (res.status == 'success') {
        Library.notify(this._translate.NOTIFY.update_success, 'success');
        this.bsModalRef.hide();
      } else {
        Library.notify(res.message, 'error');
        this.getCaptcha();
      }
      Library.hideloading()
    })
  }
  onChangeCheck() {
    this.isCheck = !this.isCheck;
  }
}
