import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {InvoiceComponent} from '@modules/invoice-buy/component/invoice/invoice.component';
import {FormResolverService} from '@modules/invoice-buy/shared/resolve/form-resolver.service';
import {IndexComponent} from '@modules/invoice-buy/component/index/index.component';

const routes: Routes = [
  {path: '', component: IndexComponent},
  {path: 'edit-invoice/:id', component: InvoiceComponent, data: {brec: 'Cp nht ha n'}, resolve: {FormResolverService}},
  {path: 'view-invoice/:id', component: InvoiceComponent, data: {brec: 'Xem ha n'}, resolve: {FormResolverService}},
  {path: 'create', component: InvoiceComponent, data: {brec: 'Thm ha n'}, resolve: {FormResolverService}},
  {path: '**', redirectTo: '/'},
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InvoiceBuyRouting {
}
