import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { AuthService } from '@core/services';
import { VatService } from '@modules/vat/shared';
import { BsModalRef, BsModalService } from 'ngx-bootstrap';
import { ActivatedRoute, Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { DxDataGridComponent, DxMenuComponent } from 'devextreme-angular';
import { Library } from '@core/library';
import { Helper } from '@core/helper/Helper';
import { VPreviewInvService } from '@core/library/invoice/v-preview-inv.service';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { UploadFileZipComponent } from '@modules/invoice-buy/shared/modal/upload-file-zip/upload-file-zip.component';
import { empty, forkJoin, from } from 'rxjs';
import { SocketService, sEnum } from '@core/index';
import { Subscription } from 'rxjs/Subscription';
import { ViewInvoiceMultipageComponent } from '@shared/index';
import { Invoice } from '@modules/invoice-buy/shared/class/invoice.model';
import { NgxSpinnerService } from 'ngx-spinner';
import { VSelectBoxService } from '@core/library/public/v-select-box.service';
import { format } from 'url';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-list-invoice',
  templateUrl: './list-invoice.component.html',
  styleUrls: ['./list-invoice.component.scss']
})
export class ListInvoiceComponent implements OnInit {
  @ViewChild(DxMenuComponent) dxMenu: DxMenuComponent;
  @ViewChild(DxDataGridComponent) dataGrid: DxDataGridComponent;
  @Input() isHasPackage;
  @ViewChild('expandMenu') expandMenu;
  // dataSource: DataSource;
  dataSource: any;
  selectedRows: Array<any> = [];
  selectedItems: Array<any> = [];
  bsModalRef: BsModalRef;
  listDM: any;
  loaiHoaDon: string;
  test = true;
  isAccessExportInv = true;
  invoice_releases = [];
  paramSearch: string = null;
  _language: string;
  _translate: any;
  checkSubmit: any;
  extras = [];
  extendInvoiceBuy: any = [];
  data_extras_invoice_buy = [];
  extend = [];
  select: any = {};
  configs: any;
  disEditMulti = true;
  system_parameter: any = {};
  clickCurent = '';
  isSearchUI: boolean;
  isInvoiceBuy = true
  dataAddInvoiceBuy = [];
  paramsExportData: string = null;
  ioSignature: Subscription;
  frmData: Invoice = new Invoice('1');
  testResults: any;
  adjustmentTypes: any;
  pageSelect: any;

  constructor(
    private authService: AuthService,
    private vatService: VatService,
    private modalService: BsModalService,
    private modalImportService: BsModalService,
    public bsModalDocumentCancel: BsModalRef,
    private router: Router,
    private route: ActivatedRoute,
    public translate: TranslateService,
    private invoiceBuyService: InvoiceBuyService,
    private socketService: SocketService,
  ) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
    this.adjustmentTypes = VSelectBoxService._adjustmentTypes(this._translate);
    this.testResults = [
      { name: 'Cha c', code: 'CHUA_CO' },
      { name: ' c', code: 'DA_CO' },
      { name: 'Cha kim tra', code: 'CHUA_KIEM_TRA' }
    ];
    this.data_extras_invoice_buy = [{
      id: '1',
      name: this._translate.CONTROLL.LABEL.export_data,
      items: [
        {
          id: 'LIST_INVOICE_BUY',
          name: this._translate.CONTROLL.LABEL.list_invoice_buy,
          icon: this._translate.ICONS.excel
        },
        {
          id: 'DETAILS_OF_EACH_GOODS_AND_SERVICE',
          name: this._translate.CONTROLL.LABEL.details_of_each_goods_and_service,
          icon: this._translate.ICONS.edit
        },
      ]
    }];
    this.dataAddInvoiceBuy = [{
      id: '1',
      name: this._translate.CONTROLL.BUTTON.add,
      items: [
        {
          id: 'ADD_ONE_INVOICE',
          name: this._translate.CONTROLL.LABEL.add_one_invoice,
          icon: this._translate.ICONS.add_new
        },
        {
          id: 'ADD_MULTIPLE_INVOICE',
          name: this._translate.CONTROLL.LABEL.add_multiple_invoice,
          icon: this._translate.ICONS.add_new
        },
      ]
    }];
    this.extendInvoiceBuy = [{
      id: '1',
      name: 'M rng',
      items: [
        {
          id: 'DELETE_MULTIPLE_INVOICE',
          name: this._translate.CONTROLL.BUTTON.delete,
          icon: this._translate.ICONS.delete
        },
        {
          id: 'CHECK_MULTIPLE_INVOICE',
          name: this._translate.CONTROLL.BUTTON.check_mutiple_invoice,
          icon: this._translate.ICONS.tct_icon
        },
      ]
    }];
  }

  ngOnInit() {
    this.configs = this.authService.getConfigs();
    if (this.configs.system_parameter.select_invoice_current_page)
      this.pageSelect = "page";
    else
      this.pageSelect = "allPages";
    this.connectWebsocket();
    this.loadList();
  }
  ngOnDestroy() {
    this.ioSignature.unsubscribe();
    this.isInvoiceBuy = false;
  }
  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
    });
  }
  getSearch(e) {
    this.paramSearch = e;
    this.loadList(e);
  }

  async itemExtrasClick(e) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.hoadonmuavao);
    if (!isCheckInitFee)
      return;
    if (e.itemData.id === 'ADD_ONE_INVOICE') {
      if (this.isHasPackage) {
        this.router.navigate(['/system/invoice-buy/create']);
      } else {
        Library.notify("Vui lng mua gi ha n u vo  s dng chc nng ny", "error")
      }
    } else if (e.itemData.id === 'ADD_MULTIPLE_INVOICE') {
      if (this.isHasPackage) {
        this.importMultipleFile();
      } else {
        Library.notify("Vui lng mua gi ha n u vo  s dng chc nng ny", "error")
      }
    } else if (e.itemData.id === 'LIST_INVOICE_BUY') {
      this.exportXLXS();
    } else if (e.itemData.id === 'DETAILS_OF_EACH_GOODS_AND_SERVICE') {
      this.exportDetails();
    } else if (e.itemData.id === 'DELETE_MULTIPLE_INVOICE') {
      this.delete();
    }
    else if (e.itemData.id === 'CHECK_MULTIPLE_INVOICE') {
      this.checkInvoiceTCT();
    }
  }
  // xu ly cho export XML chua xong
  setParameExport() {
    let dataValue;
    const sort = '&sort[0][key]=invoice_date&sort[0][direction]=DESC';
    const period = this.route.snapshot.queryParams.exportSchedulesDefault;
    if (period === 'TAT_CA') {
      dataValue = period + this.paramsExportData + sort;
    } else {
      const from_date = this.route.snapshot.queryParams.startDate;
      const to_date = this.route.snapshot.queryParams.endDate;
      dataValue = `${period}&options[from_date]=${from_date}&options[to_date]=${to_date}` + this.paramsExportData + sort;
    }
    return dataValue;
  }

  exportXLXS() {
    Library.showloading();
    this.invoiceBuyService.apiExportListInvoicebuy(this.setParameExport())
      .subscribe(res => {
        if (res.status === 'success') {
          const file = res.data.report;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
          Library.notify('Xut file Excel thnh cng', 'success');
        } else {
          Library.notify(res.message, 'error');
        }
      }, error => Library.notify(error, 'error'),
        () => Library.hideloading()
      );
  }

  exportDetails() {
    Library.showloading();
    this.invoiceBuyService.apiExportDetailInvoicebuy(this.setParameExport())
      .subscribe(res => {
        if (res.status === 'success') {
          const file = res.data.report;
          Helper.dowloadFile(file.file_name, 'xlsx', file.file_content);
          Library.notify('Xut file thnh cng', 'success');
        } else {
          Library.notify(res.message, 'error');
        }
      }, error => Library.notify(error, 'error'),
        () => Library.hideloading()
      );
  }

  async importMultipleFile() {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.hoadonmuavao);
    if (!isCheckInitFee)
      return;
    this.bsModalDocumentCancel = this.modalService.show(UploadFileZipComponent, {
      class: 'modal-lg full-modal', backdrop: 'static',
      keyboard: false
    });

    this.modalService.onHide.subscribe(
      () => {
        if (this.bsModalDocumentCancel.content.LoadList === 'LoadList') {
          this.loadList();
          delete this.bsModalDocumentCancel.content.LoadList;
        }
      });
  }

  async edit(data) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.hoadonmuavao);
    if (!isCheckInitFee)
      return;
    const row = data ? data : this.selectedItems[0];
    this.router.navigate([`/system/invoice-buy/edit-invoice/${row.id}`]);
  }

  async delete(data?) {
    let isCheckInitFee = await this.authService.checkInitialFee(sEnum.initial_fee.hoadonmuavao);
    if (!isCheckInitFee)
      return;
    if (this.selectedItems.length || data) {
      let objData = {};
      const ids = [];
      const idsEmail = [];
      const isInvoiveInMail = this.selectedItems.filter(item => item.input_invoice_mail_id !== null)
      if (data) {
        ids.push(data.id);
        objData = {
          ids: data.input_invoice_mail_id,
          error_reason: "Ngi dng  xa ha n t danh sch ha n u vo",
        }
      } else {
        this.selectedItems.map((item) => {
          ids.push(item.id);
          if (item.input_invoice_mail_id) {
            idsEmail.push(item.input_invoice_mail_id)
          }
        });
      }
      if (Array.isArray(ids) && ids.length) {
        const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
        result.then((dialogResult) => {
          if (dialogResult) {
            Library.showloading();
            this.invoiceBuyService.deleteInvoiceId(ids).subscribe(rs => {
              if (rs.status === 'success') {
                if (data) {
                  if (data.input_invoice_mail_id) {
                    let ids = [];
                    ids.push(data.input_invoice_mail_id)
                    objData = {
                      ids: ids,
                      error_reason: "Ngi dng  xa ha n t danh sch ha n u vo",
                    }
                    this.invoiceBuyService.notReceived(objData).subscribe()
                  }
                } if (isInvoiveInMail.length > 0) {
                  objData = {
                    ids: idsEmail,
                    error_reason: "Ngi dng  xa ha n t danh sch ha n u vo",
                  }
                  this.invoiceBuyService.notReceived(objData).subscribe()
                }
                Library.notify(this._translate.NOTIFY.delete_success, 'success');
                this.loadList();
              } else {
                Library.notify(rs.message, 'error');
              }
            }, err => {
            }, () => {
              Library.hideloading();
            });
          }
        });
      } else {
        Library.notify('Khng c xa ha n t phn mm ihoadon', 'error');
      }
    }
  }

  customizeText(e) {
    const formatter = new Intl.NumberFormat('ja-JP');
    let totalMoney = '0';
    if (e && e.value) {
      const _arr = e.value.split('&');
      if (_arr.length > 1) {
        // totalMoney = "TTH bn ra:" + '\n' + formatter.format(parseFloat(_arr[0] || "0"));
        totalMoney = formatter.format(parseFloat(_arr[0] || '0'));
        // if (_arr[1] != "")
        //   totalMoney += '\n' + "TTH gim tr:" + '\n' + formatter.format(parseFloat(_arr[1] || "0"));
      }
    }
    return totalMoney;
  }

  selectionChanged(data: any) {
    if (this.clickCurent) {
      document.getElementById(this.clickCurent).classList.remove('show');
    }
    this.disEditMulti = true;
    this.selectedItems = data.selectedRowsData;
    if (this.selectedItems.length > 0) {
      const checks = this.selectedItems.filter((item) => {
        return item.number_of_vouchers;
      });
      const checkDaXuat = this.selectedItems.filter((item) => {
        return item.status === 'DA_XUAT';
      });
      if (checks.length === this.selectedItems.length && checkDaXuat.length === 0) {
        this.disEditMulti = false;
      }
    }
  }

  onRowPrepared(e) {
    if (e.rowType === 'data') {
      switch (e.data.status) {
        case 'DA_XUAT':
          break;
        case 'GHI_TAM':
          if (e.data.invoice_number) {
            e.rowElement.style.color = 'red';
          } else {
            e.rowElement.style.color = '#DEA960';
          }
          break;
        case 'GIU_SO':
          e.rowElement.style.color = '#008B8B';
          break;
        default:
          break;
      }
      if (e.data.adjustment_type === '2' || e.data.adjustment_type === '7' || e.data.adjustment_type === '6') {
        const eles = e.rowElement.getElementsByTagName('td');
        for (let i = 0; i < eles.length; i++) {
          if (i !== 9 && i !== 1) {
            eles[i].style.textDecoration = 'line-through';
          }
        }
      }
    }
  }

  loadList(params?) {
    this.dataSource = {};
    const ref = {};
    if (params) {
      let data = params;
      if (data.search('search_date') !== -1) {
        data = data.replace(/search_date/gi, 'invoice_date');
      }
      if (data.search('customer_name') !== -1) {
        data = data.replace(/customer_name/gi, 'seller_legal_name');
      }
      if (data.search('buyer_tax_code') !== -1) {
        data = data.replace(/buyer_tax_code/gi, 'seller_tax_code');
      }
      if (data.search('invoice_products.product_name') !== -1) {
        data = data.replace(/invoice_products.product_name/gi, 'input_invoice_products.product_name');
      }
      if (data.search('approve_status') !== -1) {
        data = data.replace(/approve_status/gi, 'export_from_ihd');
      }
      const valueParam = data;
      this.paramsExportData = valueParam;
      this.dataSource.store = this.invoiceBuyService.getAll(valueParam);
    } else {
      this.dataSource.store = this.invoiceBuyService.getAll();
    }
  }

  view(data) {
    const row = data ? data : this.selectedItems[0];
    Library.showloading();
      let sub = this.invoiceBuyService.getInvoiceBuy(row.id).subscribe(
        res => {
          if (res.status === 'success') {
            let fileData: Array<any> = [];
            let obj = new Object;
            obj["id"] = Helper.makeid();
            obj["file_name"] = "ihoadon_efyvietnam_9710";
            obj["file_server_id"] = Helper.makeid();
            obj["input_invoice_id"] = Helper.makeid();
            fileData.push(obj);
            this.frmData = res.data.input_invoice;
            let lstFile = res.data.input_invoice.input_invoice_files;
            if (lstFile != null && lstFile.length > 0) {
              lstFile.forEach(item => {
                if (!item.file_name.includes('.xml'))
                  fileData.push(item);
              });
            }
            let ids = [];
            ids.push(row.id);
            let initialState = {
              items: fileData,
              isXuatHD: false,
              hinhthucxuat: '',
              typeAction: sEnum.typeCode.hoa_don_mua_vao_khac,
              frmData: this.frmData,
              fileData: lstFile
            };
            if (row.export_from_ihd == "1") {
              initialState = {
                items: ids,
                isXuatHD: false,
                hinhthucxuat: '',
                typeAction: sEnum.typeCode.hoa_don_mua_vao_efy,
                frmData: this.frmData,
                fileData: fileData
              };
            }
            if (data.export_from_tct == '1') {
              initialState = {
                items: fileData,
                isXuatHD: false,
                hinhthucxuat: '',
                typeAction: sEnum.typeCode.hoa_don_mua_vao_khac,
                frmData: this.frmData,
                fileData: lstFile,
              };
            }
            this.bsModalRef = this.modalImportService.show
              (
                ViewInvoiceMultipageComponent,
                {
                  backdrop: 'static',
                  keyboard: false,
                  class: 'modal-xl view-invoice',
                  initialState
                }
              );
            const subModal = this.modalImportService.onHide.subscribe(() => {
              subModal.unsubscribe();
            });
          }
          else {
            Library.notify(res.message, 'error');
          }
        },
        error => {
          Library.notify(error, 'error');
        }, () => {
          Library.hideloading();
        }
      );
  }

  setUrlNavigate() {
    const url = window.location.pathname;
    if (sessionStorage.hasOwnProperty('urlIndex')) {
      if (sessionStorage.getItem('urlIndex') !== null) {
        sessionStorage.setItem('urlIndex', url);
      } else {
        sessionStorage.setItem('urlIndex', url);
      }
    } else {
      sessionStorage.setItem('urlIndex', url);
    }
  }
  search(data) {
    if (data.export_from_ihd == '1') {
      const win = window.open('https://ihoadon.vn/kiem-tra/?shd=' + data.invoice_number + "&mtc=" + data.lookup_code, '_blank');
      win.focus();
    }
    else {
      if (data.lookup_link) {
        const win = window.open(data.lookup_link, '_blank');
        win.focus();
      }
      else
        Library.notify("Ha n cha nhp link tra cu. Vui lng kim tra li", 'error');
    }
  }
  checkInvoiceTCT() {
    Library.showloading()
    let data:any = {
      // datas: []
    }
    if (this.selectedItems.length > 0) {
      let data = [];
      this.selectedItems.forEach((item) => {
        data.push(this.invoiceBuyService.checkInputInvoiceTct({id: item.id ? item.id : null}))
      })

      forkJoin(data).subscribe(
        (result) => {
          Library.notify("Kim tra thnh cng", 'success');
          this.loadList(this.paramSearch);
          this.dataGrid.instance.clearSelection();
        },
        (error) => {
          Library.notify(error, 'error');
        },
        () => {
          Library.hideloading();
        }

      )
    } else {
      Library.notify("Vui lng chn ha n", 'error');
    }
  }


  calculateFilterExpression(filterValue, selectedFilterOperation) {
    selectedFilterOperation = "contains"; //Helper.multilfilterColumn;
    return [["invoice_series", selectedFilterOperation || '=', filterValue],'or', ["template_code",selectedFilterOperation || '=', filterValue]];
  }
}
