import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AuthService } from '@core/services';
import { TranslateService } from '@ngx-translate/core';
import { Helper } from '@core/helper/Helper';
import { Library } from '@core/library';

declare var $: any;

@Component({
  selector: 'app-file-upload-invoice-buy',
  templateUrl: './file-upload-invoice-buy.component.html',
  styleUrls: ['./file-upload-invoice-buy.component.scss']
})
export class FileUploadInvoiceBuyComponent implements OnInit {

  @Output('fileChange') fileChange = new EventEmitter<Array<any>>();
  @Input() fileData: Array<any>;
  @Input() frmData: any;
  @Input() invoiceExp: any;
  _translate: any;
  extFile = ".xlsx,.docx,.pdf,.xml";

  constructor(
    private authService: AuthService,
    public translate: TranslateService
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    if (this.invoiceExp == "MO_RONG_FILE")
      this.extFile = ".xlsx,.docx,.pdf,.xml,.html";
    $('.va-file-upload').on('drag dragstart dragend dragover dragenter dragleave drop', function (e) {
      e.preventDefault();
      e.stopPropagation();
    })
      .on('dragover dragenter', (e) => {
        $('.va-file-upload').addClass('is-dragover');
      })
      .on('dragleave dragend drop', (e) => {
        $('.va-file-upload').removeClass('is-dragover');
      })
      .on('drop', (e) => {
        const file = e.originalEvent.dataTransfer.files;
        this.dropFileChanged(file[0]);
      });
  }

  removeFile(i, fileName) {
    if (this.frmData.invoice_type_name === fileName) {
      Library.notify('Error', 'error');
      return;
    } else {
      this.fileData.splice(i, 1);
      this.fileChange.emit(this.fileData);
    }
  }

  view(file) {
    const arrFile: Array<any> = file.file_name.split('.');
    Helper.dowloadFile(file.file_name, arrFile[arrFile.length], file.file_content);
  }

  choseFile() {
    document.getElementById('file-input').click();
  }

  choseFile1() {
    document.getElementById('file-input1').click();
  }

  fileChanged(e) {
    const file = e.target.files[0];
    if (e.target.files[0].size <= 2000000) {
      const extension = file.name.split('.').pop();
      if (extension === 'docx' || extension === 'xlsx' || extension === 'pdf' || extension === 'xml' || extension === 'html') {
        const fileReader = new FileReader();
        fileReader.readAsDataURL(file);
        fileReader.onload = (e) => {
          const base64result = fileReader.result.toString().split(',')[1];
          this.fileData.unshift({
            order: 'BK',
            file_name: file.name,
            file_content: base64result,
          });
          if (document.getElementById('list-file')) {
            document.getElementById('list-file').scrollTop = 0;
          }
          this.fileChange.emit(this.fileData);
        };
      } else {
        Library.notify(this._translate.NOTIFY.file_upload_no_access + ", html", 'error');
      }
    } else {
      Library.notify('Yu cu file nh hn 2MB', 'error');
    }
  }

  dropFileChanged(file) {
    const extension = file.name.split('.').pop();
    if (extension === 'docx' || extension === 'xlsx' || extension === 'pdf' || extension === 'xml' || extension === 'html') {
      const fileReader = new FileReader();
      fileReader.readAsDataURL(file);
      fileReader.onload = (e) => {
        const base64result = fileReader.result.toString().split(',')[1];
        this.fileData.unshift({
          type: 'BK',
          file_name: file.name,
          file_content: base64result,
        });
        if (document.getElementById('list-file')) {
          document.getElementById('list-file').scrollTop = 0;
        }
        this.fileChange.emit(this.fileData);
      };
    } else {
      Library.notify(this._translate.NOTIFY.file_upload_no_access + ", html", 'error');
    }
  }

}
