import { Component, ElementRef, EventEmitter, Input, OnInit, Output, Renderer2, ViewChild, AfterViewInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { VSelectBoxService, VStringService } from '@core/index';
import { AuthService } from '@core/services';
import { InvoiceBuyService } from '@modules/invoice-buy/invoice-buy.service';
import { TranslateService } from '@ngx-translate/core';
import * as moment from "moment";
import { collapseTextChangeRangesAcrossMultipleVersions } from 'typescript';
import { isBuffer } from 'util';

@Component({
  selector: 'app-search-mail',
  templateUrl: './search-mail.component.html',
  styleUrls: ['./search-mail.component.scss']
})
export class SearchMailComponent implements OnInit, AfterViewInit {
  opened = false;
  mailAccount: any;
  _language: string;
  objSelect: any = {};
  _translate: any;
  defaultEmailAccount: any;
  defaultStatus: any
  firstEmaiAccountId: any;
  searchString: any;
  arrSelected: any = [];
  status: any;
  isShow: any
  isEndDateChange = false;
  isStartDateChange = false;
  isEmailNameChange = false;
  isSearch = false;
  isSubject = false;
  curentMail: any;
  curentUser: any;
  nonSearch: any;
  emailForm = new FormGroup({
    emailName: new FormControl(''), // <== default value
    status: new FormControl(''),
    send_from: new FormControl(''),
    endDate: new FormControl(''),
    startDate: new FormControl(''),
    subject: new FormControl('') // <== default value
  });
  @Input() isSelected;
  @Input() isMailReceived;
  @Output("searchContentChange") searchContentChange = new EventEmitter<any>();
  @Output("selectMail") selectMail = new EventEmitter<any>();
  @Output("isMail") isMail = new EventEmitter<any>();
  @ViewChild('toggleSearch') toggleSearch: ElementRef;
  @ViewChild('menuSearch') menuSearch: ElementRef;
  constructor(private invoiceBuyService: InvoiceBuyService, private authService: AuthService,
    public translate: TranslateService, private fb: FormBuilder, private renderer: Renderer2) {
    this._language = this.authService.getLanguage();
    this._translate = this.authService.getTranslate();
    translate.use(this._language);
    this.status = VSelectBoxService.statusEmail(this._translate);
    this.defaultStatus = null;
    this.curentUser = this.authService.getCurrentUser();
    this.curentMail = this.curentUser.user.permission_mails;

  }
  ngOnChanges() {
    if (this.isSelected) {
      this.nonSearch = 'none-search';
    } else {
      this.nonSearch = ''
    }
  }
  ngOnInit() {
    if (this.isMailReceived.id == null) {
      let filter = '';
      if (this.curentMail !== "" && this.curentMail !== null) {
        let permission_mails = this.curentMail.split(',');
        filter += `&filter_groups[1][filters][0][key]=email`
        for (let i = 0; i < permission_mails.length; i++) {
          filter += `&filter_groups[1][filters][0][value][${i}]=${permission_mails[i]}`
        }
        filter += `&filter_groups[1][filters][0][operator]=in&`
      } else {

      }
      this.invoiceBuyService.getEmailAccount(filter).subscribe(res => {
        this.mailAccount = res.data.config_mails;
        if (this.mailAccount.length > 0) {
          this.isMail.emit(true)
        } else {
          this.isMail.emit(false)
        }
        this.emailForm = this.fb.group({
          emailName: res.data.config_mails[0].id,
          status: 'TAT_CA',
          send_from: '',
          endDate: '',
          startDate: '',
          subject: ''
        });
        this.onSubmit()
      });
    } else {
      let filter = '';
      if (this.curentMail !== "" && this.curentMail !== null) {
        let permission_mails = this.curentMail.split(',');
        filter += `&filter_groups[1][filters][0][key]=email`
        for (let i = 0; i < permission_mails.length; i++) {
          filter += `&filter_groups[1][filters][0][value][${i}]=${permission_mails[i]}`
        }
        filter += `&filter_groups[1][filters][0][operator]=in&`
      } else {

      }
      this.invoiceBuyService.getEmailAccount(filter).subscribe(res => {
        this.mailAccount = res.data.config_mails;
        if (this.mailAccount.length > 0) {
          this.isMail.emit(true)
        } else {
          this.isMail.emit(false)
        }
        this.emailForm = this.fb.group({
          emailName: this.isMailReceived.id,
          status: 'TAT_CA',
          send_from: '',
          endDate: '',
          startDate: '',
          subject: ''
        });
        this.onSubmit()
      });
    }
    setTimeout(() => {
      this.renderer.listen('window', 'click', (e: Event) => {
        if (!this.toggleSearch.nativeElement.contains(e.target) && !this.menuSearch.nativeElement.contains(e.target)) {
          this.opened = false;
        }
      });
    }, 500);
  }
  ngAfterViewInit() {

  }
  onEmailChanged(e) {
    if (e.event) {
      this.onSubmit();
    }
  }
  onEndDate(e) {
    if (e.event) {
      this.isEndDateChange = true;
    }
  }

  onStartDate(e) {
    if (e.event) {
      this.isStartDateChange = true;
    }
  }
  onEmailNameChange(e) {
    if (e.event) {
      this.isEmailNameChange = true;
    }
  }
  valueChanged(e) {
    if (e.event) {
      this.onSubmit();
    }
  }
  onStatusChanged(e) {
    if (e.event) {
      this.onSubmit()
    }
  }
  onSubmit() {
    if (this.isMailReceived.listDelete) {
      if (this.emailForm.get('status').value == "TAT_CA") {
        let selectString = `filter_groups[1][filters][0][key]=config_mail_id
        &filter_groups[1][filters][0][value]=${this.emailForm.get('emailName').value}
        &filter_groups[1][filters][0][operator]=eq&
        &filter_groups[2][filters][1][key]=soft_del
        &filter_groups[2][filters][1][value]=null
        &filter_groups[2][filters][1][operator]=eq&
        `;
        if (this.emailForm.get('subject').value !== '') {
          selectString += `filter_groups[3][filters][0][key]=subject
          &filter_groups[3][filters][0][value]=${this.emailForm.get('subject').value}
          &filter_groups[3][filters][0][operator]=ct&`
        }
        if (this.emailForm.get('startDate').value !== '' && this.emailForm.get('startDate').value !== null) {
          selectString += `&filter_groups[4][filters][1][key]=send_date
            &filter_groups[4][filters][1][value]=${moment(this.emailForm.get('startDate').value).format("YYYY-MM-DD 00:00:00")}
            &filter_groups[4][filters][1][operator]=gte&`
        }
        if (this.emailForm.get('endDate').value !== '' && this.emailForm.get('endDate').value !== null) {
          selectString += `&filter_groups[5][filters][1][key]=send_date
            &filter_groups[5][filters][1][value]=${moment(this.emailForm.get('endDate').value).format("YYYY-MM-DD 23:59:59")}
            &filter_groups[5][filters][1][operator]=lt&`
        }
        if (this.emailForm.get('send_from').value !== '') {
          let emailName = this.emailForm.get('send_from').value;
          let valueSearch = emailName.split(';')
          if (valueSearch.length === 1) {
            selectString += `&filter_groups[6][filters][1][key]=send_from
              &filter_groups[6][filters][1][value]=${this.emailForm.get('send_from').value}
              &filter_groups[6][filters][1][operator]=eq&`
          } else {
            selectString += `&filter_groups[6][filters][1][key]=send_from`
            for (let i = 0; i < valueSearch.length; i++) {
              selectString += ` &filter_groups[6][filters][1][value][${i}]=${valueSearch[i]}`
            }
            selectString += ` &filter_groups[6][filters][1][operator]=in&`
          }
        }
        this.searchContentChange.emit(selectString);
        let id = this.emailForm.get("emailName").value
        this.selectMail.emit(id)
      } else {
        let selectString = `filter_groups[1][filters][0][key]=config_mail_id
        &filter_groups[1][filters][0][value]=${this.emailForm.get('emailName').value}
        &filter_groups[1][filters][0][operator]=eq&
        filter_groups[8][filters][0][key]=status
        &filter_groups[8][filters][0][value]=${this.emailForm.get('status').value}
        &filter_groups[8][filters][0][operator]=eq&
        &filter_groups[2][filters][1][key]=soft_del
        &filter_groups[2][filters][1][value]=null
        &filter_groups[2][filters][1][operator]=eq&
        `;
        if (this.emailForm.get('subject').value !== '') {
          selectString += `filter_groups[3][filters][0][key]=subject
          &filter_groups[3][filters][0][value]=${this.emailForm.get('subject').value}
          &filter_groups[3][filters][0][operator]=ct&`
        }
        if (this.emailForm.get('startDate').value !== '' && this.emailForm.get('startDate').value !== null) {
          selectString += `&filter_groups[4][filters][1][key]=send_date
            &filter_groups[4][filters][1][value]=${moment(this.emailForm.get('startDate').value).format("YYYY-MM-DD 00:00:00")}
            &filter_groups[4][filters][1][operator]=gte&`
        }
        if (this.emailForm.get('endDate').value !== '' && this.emailForm.get('endDate').value !== null) {
          selectString += `&filter_groups[5][filters][1][key]=send_date
            &filter_groups[5][filters][1][value]=${moment(this.emailForm.get('endDate').value).format("YYYY-MM-DD 23:59:59")}
            &filter_groups[5][filters][1][operator]=lt&`
        }
        if (this.emailForm.get('send_from').value !== '') {
          selectString += `&filter_groups[6][filters][1][key]=send_from
            &filter_groups[6][filters][1][value]=${this.emailForm.get('send_from').value}
            &filter_groups[6][filters][1][operator]=eq&`
        }
        this.searchContentChange.emit(selectString);
        let id = this.emailForm.get("emailName").value
        this.selectMail.emit(id)
      }
    }
    else {
      if (this.emailForm.get('status').value == "TAT_CA") {
        let selectString = `filter_groups[1][filters][0][key]=config_mail_id
        &filter_groups[1][filters][0][value]=${this.emailForm.get('emailName').value}
        &filter_groups[1][filters][0][operator]=eq&
        &filter_groups[2][filters][1][key]=soft_del
        &filter_groups[2][filters][1][value]=1
        &filter_groups[2][filters][1][operator]=eq&
        `;
        if (this.emailForm.get('subject').value !== '') {
          selectString += `filter_groups[3][filters][0][key]=subject
          &filter_groups[3][filters][0][value]=${this.emailForm.get('subject').value}
          &filter_groups[3][filters][0][operator]=ct&`
        }
        if (this.emailForm.get('startDate').value !== '' && this.emailForm.get('startDate').value !== null) {
          selectString += `&filter_groups[4][filters][1][key]=send_date
            &filter_groups[4][filters][1][value]=${moment(this.emailForm.get('startDate').value).format("YYYY-MM-DD 00:00:00")}
            &filter_groups[4][filters][1][operator]=gte&`
        }
        if (this.emailForm.get('endDate').value !== '' && this.emailForm.get('endDate').value !== null) {
          selectString += `&filter_groups[5][filters][1][key]=send_date
            &filter_groups[5][filters][1][value]=${moment(this.emailForm.get('endDate').value).format("YYYY-MM-DD 23:59:59")}
            &filter_groups[5][filters][1][operator]=lt&`
        }
        if (this.emailForm.get('send_from').value !== '') {
          selectString += `&filter_groups[6][filters][1][key]=send_from
            &filter_groups[6][filters][1][value]=${this.emailForm.get('send_from').value}
            &filter_groups[6][filters][1][operator]=eq&`
        }

        this.searchContentChange.emit(selectString);
        let id = this.emailForm.get("emailName").value
        this.selectMail.emit(id)
      } else {
        let selectString = `filter_groups[1][filters][0][key]=config_mail_id
        &filter_groups[1][filters][0][value]=${this.emailForm.get('emailName').value}
        &filter_groups[1][filters][0][operator]=eq&
        filter_groups[8][filters][0][key]=status
        &filter_groups[8][filters][0][value]=${this.emailForm.get('status').value}
        &filter_groups[8][filters][0][operator]=eq&
        &filter_groups[2][filters][1][key]=soft_del
        &filter_groups[2][filters][1][value]=1
        &filter_groups[2][filters][1][operator]=eq&
        `;
        if (this.emailForm.get('subject').value !== '') {
          selectString += `filter_groups[3][filters][0][key]=subject
          &filter_groups[3][filters][0][value]=${this.emailForm.get('subject').value}
          &filter_groups[3][filters][0][operator]=ct&`
        }
        if (this.emailForm.get('startDate').value !== '' && this.emailForm.get('startDate').value !== null) {
          selectString += `&filter_groups[4][filters][1][key]=send_date
            &filter_groups[4][filters][1][value]=${moment(this.emailForm.get('startDate').value).format("YYYY-MM-DD 00:00:00")}
            &filter_groups[4][filters][1][operator]=gte&`
        }
        if (this.emailForm.get('endDate').value !== '' && this.emailForm.get('endDate').value !== null) {
          selectString += `&filter_groups[5][filters][1][key]=send_date
            &filter_groups[5][filters][1][value]=${moment(this.emailForm.get('endDate').value).format("YYYY-MM-DD 23:59:59")}
            &filter_groups[5][filters][1][operator]=lt&`
        }
        if (this.emailForm.get('send_from').value !== '') {
          selectString += `&filter_groups[6][filters][1][key]=send_from
            &filter_groups[6][filters][1][value]=${this.emailForm.get('send_from').value}
            &filter_groups[6][filters][1][operator]=eq&`
        }
        this.searchContentChange.emit(selectString);
        let id = this.emailForm.get("emailName").value
        this.selectMail.emit(id)
      }
    }
  }
  open() {
    this.opened = !this.opened
  }
  search() {
    this.onSubmit();
    this.opened = false;
    this.isEndDateChange = false;
    this.isEmailNameChange = false;
    this.isStartDateChange = false
  }
}
